 <template>

    <div id="ContentZone" 
        ref="root" 
        class="base"
    >
        <div class="leftZone" ref="leftZone"></div>
        <div class="middleZone" ref="middleZone">
            <transition 
                name="mainContent" 
                mode="out-in"
                @enter="OnTransitionEnter"
                @after-enter="OnTransitionEntered"
                >
                <keep-alive>
                    <router-view 
                        @onRouterComBeMounted="OnRouterChanged"
                        @onNewsStyleBoxContainerContentChanged="OnNewsStyleBoxContainerContentChanged"
                        placeID=1
                        name="content"
                        ref="mainRouterView"
                        >
                    </router-view>
                </keep-alive>
            </transition>
        </div>
        <div class="rightZone" ref="rightZone"></div>
    </div>
    
 </template>

<script>

let o_windowResizeEventMap = new Map();
let n_leftRightGapWidth = 50;

import VNewStyleBoxContainer from "../view/NewsStyleBox/NewsStyleBoxContainer.vue";

export default {

    name: "ContentZone",

    data(){
        return {
            
        }
    },

    methods:{

        ResizeContentHeight(){
            this.$nextTick(()=>{
                let curMiddleHeight = this.$refs.middleZone.offsetHeight;
                this.$refs.leftZone.style.height = curMiddleHeight + "px";
                this.$refs.rightZone.style.height = curMiddleHeight + "px";
            });
        },

        ReSizeContentWidth(){
            this.$nextTick(()=>{
                let clientWidth = document.body.clientWidth;
                let widthPercentage = n_leftRightGapWidth / clientWidth * 100;
                widthPercentage = parseInt(widthPercentage);
                this.$refs.leftZone.style.width = widthPercentage + "%";
                this.$refs.rightZone.style.width = widthPercentage + "%";
                this.$refs.middleZone.style.width = (100-widthPercentage * 2) + "%";
            }); 
        },

        OnRouterChanged(o_vueCom){
            o_vueCom.o_parent = this;
        },

        OnNewsStyleBoxContainerContentChanged(){
            this.ResizeContentHeight();
        },

        Return(){
            if(this.$refs.mainRouterView.OnReturn){
                this.$refs.mainRouterView.OnReturn();
            }
        },

        /// events

        OnTransitionEnter(){
            this.ResizeContentHeight();
        },

        OnTransitionEntered(){
            this.ResizeContentHeight();
        }
    },

    components:{},

    mounted(){
        this.$nextTick(()=>{
            let offsetHeight = this.$refs.root.offsetHeight;
            this.$refs.middleZone.style["min-height"] = offsetHeight + "px";
            this.ResizeContentHeight();
        });
        
        this.ReSizeContentWidth();

        o_windowResizeEventMap.set(this,()=>{
            this.ReSizeContentWidth();
            this.ResizeContentHeight();
        });
    },

    updated(){
        this.ResizeContentHeight();
    },

    destroyed(){
        o_windowResizeEventMap.delete(this);
    },

    /// other
    OnWindowResize(){
        let keys = o_windowResizeEventMap.keys();
        let key = keys.next().value;
        while(key){
            o_windowResizeEventMap.get(key).apply(key);
            key = keys.next().value;
        }
        VNewStyleBoxContainer.OnWindowResize();
    }
}

</script>

<style lang="scss" scoped>

.base{
}

.leftZone{
    width:50px;
    float:left;
    background-color: rgb(215, 220, 224);
    position: relative;
}

.middleZone{
    float:left;
    background-color: white;
    position: relative;
}

.rightZone{
    width: 50px;
    float: right;
    background-color: rgb(215, 220, 224);
    position: relative;
}

.mainContent-enter, .mainContent-leave-to{
    opacity: 0;
}

.mainContent-enter-active{
    transition: opacity 0.2s ease-out;
}

.mainContent-leave-active{
    transition: opacity 0.8s ease-in;
}

</style>