import {BaseData} from "../../base/BaseData.js";

export let CSS3Syntax = new BaseData(
    "CSS3新增语法",
    
    "CSS3中的新增的一些基础语法知识。",

    `以下是CSS3新增的属性

    1.CSS3边框：
    
    （1）border-radius：CSS3圆角边框。在 CSS2 中添加圆角矩形需要技巧，我们必须为每个圆角使用不同的图片，在 CSS3 中，创建圆角是非常容易的，在 CSS3 中，border-radius 属性用于创建圆角。
    语法：
    
    /* The syntax of the first radius allows one to four values */
    /* Radius is set for all 4 sides */
    border-radius: 10px;
    
    /* top-left-and-bottom-right | top-right-and-bottom-left */
    border-radius: 10px 5%;
    
    /* top-left | top-right-and-bottom-left | bottom-right */
    border-radius: 2px 4px 2px;
    
    /* top-left | top-right | bottom-right | bottom-left */
    border-radius: 1px 0 3px 4px;
    
    /* The syntax of the second radius allows one to four values */
    /* (first radius values) / radius */
    border-radius: 10px / 20px;
    
    /* (first radius values) / top-left-and-bottom-right | top-right-and-bottom-left */
    border-radius: 10px 5% / 20px 30px;
    
    /* (first radius values) / top-left | top-right-and-bottom-left | bottom-right */
    border-radius: 10px 5px 2em / 20px 25px 30%;
    
    /* (first radius values) / top-left | top-right | bottom-right | bottom-left */
    border-radius: 10px 5% / 20px 25em 30px 35em;
    
    /* Global values */
    border-radius: inherit;
    border-radius: initial;
    border-radius: unset;
    
    （2）box-shadow：CSS3边框阴影。在 CSS3 中，box-shadow 用于向方框添加阴影。
    语法：
    
    /* Keyword values */
    box-shadow: none;
    
    /* offset-x | offset-y | color */
    box-shadow: 60px -16px teal;
    
    /* offset-x | offset-y | blur-radius | color */
    box-shadow: 10px 5px 5px black;
    
    /* offset-x | offset-y | blur-radius | spread-radius | color */
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
    
    /* inset | offset-x | offset-y | color */
    box-shadow: inset 5em 1em gold;
    
    /* Any number of shadows, separated by commas */
    box-shadow: 3px 3px red, -1em 0 0.4em olive;
    
    /* Global keywords */
    box-shadow: inherit;
    box-shadow: initial;
    box-shadow: unset;
    
    （3）border-image：CSS3边框图片。通过 CSS3 的 border-image 属性，您可以使用图片来创建边框。
    语法：
    
    /* source | slice */
    border-image: linear-gradient(red, blue) 27;
    
    /* source | slice | repeat */
    border-image: url("/images/border.png") 27 space;
    
    /* source | slice | width */
    border-image: linear-gradient(red, blue) 27 / 35px;
    
    /* source | slice | width | outset | repeat */
    border-image: url("/images/border.png") 27 23 / 50px 30px / 1rem round space;
    
    （4）border-color：直接设置边框颜色。
    语法：
    
    /* <color> values */
    border-color: red;
    
    /* vertical | horizontal */
    border-color: red #f015ca;
    
    /* top | horizontal | bottom */
    border-color: red rgb(240,30,50,.7) green;
    
    /* top | right | bottom | left */
    border-color: red yellow green blue;
    
    /* Global values */
    border-color: inherit;
    border-color: initial;
    border-color: unset;
    
    2.CSS3背景：
    
    （1）background-size：属性规定背景图片的尺寸。在 CSS3 之前，背景图片的尺寸是由图片的实际尺寸决定的。在 CSS3 中，可以规定背景图片的尺寸，这就允许我们在不同的环境中重复使用背景图片。您能够以像素或百分比规定尺寸。如果以百分比规定尺寸，那么尺寸相对于父元素的宽度和高度。
    
    （2）background-origin：属性规定背景图片的定位区域。背景图片可以放置于 content-box、padding-box 或 border-box 区域。
    
    （3）background-clip：指定背景图片从什么位置开始裁剪。
    语法：
    
    /* Keyword values */
    background-clip: border-box;
    background-clip: padding-box;
    background-clip: content-box;
    background-clip: text;
    
    /* Global values */
    background-clip: inherit;
    background-clip: initial;
    background-clip: unset;
    
    *一个神奇的例子：
    {
        background-clip: text;
        -webkit-background-clip: text;
        color: transparent;
    }
    
    3.CSS3文字效果：
    
    （1）text-shadow：在 CSS3 中，text-shadow 可向文本应用阴影。
    语法：
    
    /* offset-x | offset-y | blur-radius | color */
    text-shadow: 1px 1px 2px black; 
    
    /* color | offset-x | offset-y | blur-radius */
    text-shadow: #fc0 1px 0 10px; 
    
    /* offset-x | offset-y | color */
    text-shadow: 5px 5px #558abb;
    
    /* color | offset-x | offset-y */
    text-shadow: white 2px 5px;
    
    /* offset-x | offset-y
    /* Use defaults for color and blur-radius */
    text-shadow: 5px 10px;
    
    /* Global values */
    text-shadow: inherit;
    text-shadow: initial;
    text-shadow: unset;
    
    （2）text-overflow ： 用于当字数超出边框的时候如何展示的效果。注意该属性生效需要overflow：hidden； white-space：nowrap；两个属性同时生效。
    语法：
    
    The text-overflow property may be specified using one or two values. If one value is given, it specifies overflow behavior for the end of the line (the right end for left-to-right text, the left end for right-to-left text). If two values are given, the first specifies overflow behavior for the left end of the line, and the second specifies it for the right end of the line.
    
    Each value is specified as one of:
    
    one of the keyword values: clip, ellipsis, fade
    the function fade(), which is passed a <length> or a <percentage> to control the fade distance
    a <string>.
    
    （3）overflow-wrap : 单词太长的话就可能无法超出某个区域，允许对长单词进行拆分，并换行到下一行：p{overflow-wrap:break-word;}
    语法：
    
    /* Keyword values */
    overflow-wrap: normal;
    overflow-wrap: break-word;
    overflow-wrap: anywhere;
    
    /* Global values */
    overflow-wrap: inherit;
    overflow-wrap: initial;
    overflow-wrap: unset;
    
    （4）word-wrap : 比overflow-wrap更简单的一个属性，也是用于换行。
    语法：
    
    word-wrap: normal|break-word;
    
    4.CSS3 2D转换：
    
    （1）transform：通过 CSS3 转换，我们能够对元素进行移动、缩放、转动、拉长或拉伸。
    
    （2）translate()：元素根据给定的 left（x 坐标） 和 top（y 坐标） 位置参数从其当前位置移动。
    语法：
        
        transform：translate（50px,100px）;
        值 translate(50px,100px) ： 把元素从左侧移动 50 像素，从顶端移动 100 像素。
    
    （3）rotate()：元素顺时针旋转给定的角度。允许负值，元素将逆时针旋转。
    语法：
        
        transform:rotate(30deg);
        值 rotate(30deg) ： 把元素顺时针旋转 30 度。
    
    （4）scale():元素的尺寸根据给定的宽度（X 轴）和高度（Y 轴）会增加或减少。
    语法：
    
        参数：transform:scale(2,4);
        值 scale(2,4) ： 把宽度转换为原始尺寸的 2 倍，把高度转换为原始高度的 4 倍。
    
    （5）skew():元素根据给定的水平线（X 轴）和垂直线（Y 轴）参数转动给定的角度。
    语法：
    
        transform:skew(30deg,20deg);
        值 skew(30deg,20deg) ： 围绕 X 轴把元素转动 30 度，围绕 Y 轴转动 20 度。
    
    （6）matrix() : 用法如下
    
    matrix() 方法把所有 2D 转换方法组合在一起。
    
    matrix() 方法需要六个参数，包含数学函数，允许您：旋转、缩放、移动以及倾斜元素。 
    
    语法：
    matrix(a, b, c, d, tx, ty)
    
    5.CSS3 3D转换：
    
    （1）rotateX()：元素围绕其 X 轴以给定的度数进行旋转。transform：rotateX(120deg);
    
    （2）rotateY()：元素围绕其 Y 轴以给定的度数进行旋转。transform：rotateY(120deg);
    
    6.CSS3 过渡：当元素从一种样式变换为另一种样式时为元素添加效果。
    
    7.CSS3动画：通过 CSS3，我们能够创建动画，这可以在许多网页中取代动画图片、Flash 动画以及 JavaScript。（animate）
    
    8.CSS3多列：
    
    （1）column-count：属性规定元素应该被分隔的列数。
    
    （2）column-gap：属性规定列之间的间隔。
    
    （3）column-rule ：属性设置列之间的宽度、样式和颜色规则。
    
    9.CSS3用户界面：
    
    （1）resize：属性规定是否可由用户调整元素尺寸。（也就是会多一个可以拖动的把手）
    语法：
    
    /* Keyword values */
    resize: none;
    resize: both;
    resize: horizontal;
    resize: vertical;
    resize: block;
    resize: inline;
    
    /* Global values */
    resize: inherit;
    resize: initial;
    resize: unset;  
    
    （2）box-sizing：控制盒模型的组成模式。（简单说就是width或者height控制的宽高算不算上border，默认是不算的）
    语法：
    
    box-sizing : content-box | border-box;
    
    （3）outline：描边。（和border的区别是outline不算大小，也不一定是矩形）
    语法：
    
    /* style */
    outline: solid;
    
    /* color | style */
    outline: #f66 dashed;
    
    /* style | width */
    outline: inset thick;
    
    /* color | style | width */
    outline: green solid 3px;
    
    /* Global values */
    outline: inherit;
    outline: initial;
    outline: unset;
    
    （4）outline-offset：设置outline距离盒模型边距之间的空隙有多大。
    
    10.CSS3颜色相关：
    
    （1）opacity : 控制透明度。
    语法：
    
    opacity : <alpha-value> = <number> | <percentage>;
    
    0 : The element is fully transparent (that is, invisible).
    Any <number> : strictly between 0 and 1	The element is translucent (that is, content behind the element can be seen).
    1 (default value) : The element is fully opaque (visually solid).
    
    （2）hsl：通过色调、饱和度、亮度来指定颜色颜色值。
    
    （3）hsla：在hsl的基础上增加透明度设置。
    
    （4）rgba：基于rgb设置颜色，a设置透明度。
    
    ****************************
    
    与@相关的:
    
    1. @media
    
    使用 @media 查询，你可以针对不同的媒体类型定义不同的样式。
    @media 可以针对不同的屏幕尺寸设置不同的样式，特别是如果你需要设置设计响应式的页面，@media 是非常有用的。
    当你重置浏览器大小的过程中，页面也会根据浏览器的宽度和高度重新渲染页面。
    
    例子:
    
    如果文档宽度小于 300 像素则修改背景颜色(background-color):
    @media screen and (max-width: 300px) {
        body {
            background-color:lightblue;
        }
    }
    
    语法:
    
    @media mediatype and|not|only (media feature) {
        CSS-Code;
    }
    
    你也可以针对不同的媒体使用不同 stylesheets :
    <link rel="stylesheet" media="mediatype and|not|only (media feature)" href="mystylesheet.css">
    
    2. @import
    
        1.链接方式
        链接方式指的是使用 HTML 头部的 <head> 标签引入外部的 CSS 文件。
    
        示例：
    
        <head>
            <link rel="stylesheet" type="text/css" href="style.css">
        </head>
        这是最常见的也是最推荐的引入 CSS 的方式。使用这种方式，所有的 CSS 代码只存在于单独的 CSS 文件中，所以具有良好的可维护性。并且所有的 CSS 代码只存在于 CSS 文件中，CSS 文件会在第一次加载时引入，以后切换页面时只需加载 HTML 文件即可。
    
        2.导入方式
        导入方式指的是使用 CSS 规则引入外部 CSS 文件。
        示例：
        <style>
            @import url(style.css);
        </style>
    
    3. @font-face
        
        指定名为"myFirstFont"的字体，并指定在哪里可以找到它的URL：
        @font-face
        {
            font-family: myFirstFont;
            src: url('Sansation_Light.ttf'),
                 url('Sansation_Light.eot'); /* IE9 */
        }
    
        属性定义及使用说明
        @font-face规则，网页设计师再也不必使用的"web-safe"的字体之一。
    
        字体的名称，font - face规则：
    
        font-family: myFirstFont;
        字体文件包含在您的服务器上的某个地方，参考CSS：
    
        src: url('Sansation_Light.ttf')
        如果字体文件是在不同的位置，请使用完整的URL：
    
        src: url('http://www.w3cschool.css/css3/Sansation_Light.ttf')
        现在准备使用该字体，下面是如何使用它所有的div元素的一个例子：
    
        div
        {
            font-family: myFirstFont;
        }
        
        语法：
        @font-face
        {
            font-properties
        }
        以下实例为在div中使用的字体:
    
        div
        {
            font-family: myFirstFont;
        }
    `,
);