import {BaseData} from "../../base/BaseData.js";

export let Element = new BaseData(
    "HTML标签",
    "HTML中的一些标签知识",
    `行内元素: 不会自动换行的元素 注意:br是行内元素
    块级元素: 会自动换行的元素
    
    常用的页面的图片格式有三种，GIF、JPG、PNG。 GIF 意为Graphics Interchange format（图形交换格式）； JPEG 代表Joint Photograhic Experts Group（联合图像专家组），这种格式经常写成JPG，JPG图片的扩展名为jpg；流式网络图形格式(Portable Network Graphic Format，PNG)名称来源于非官方的“PNG's Not GIF”，是一种位图文件(bitmap file)存储格式，读成“ping”。PNG用来存储灰度图像时，灰度图像的深度可多到16位，存储彩色图像时，彩色图像的深度可多到48位，并且还可存储多到16位的α通道数据。
    还支持bmp,bmp不常见，因为这种格式虽然很清晰色彩丰富，但是所占内存很大，所以很少见，但也是支持的。
    标签图像文件格式 （ Tagged Image File Format ，简写为 TIFF ）是一种灵活的 位图 格式，主要用来存储包括照片和艺术图在内的图像。 TIFF文件格式适用于在应用程序之间和计算机平台之间的交换文件，它的出现使得图像数据交换变得简单。 TIFF文件以 .tif 为扩展名。其数据格式是一种3级体系结构，从高到低依次为：文件头、一个或多个称为IFD的包含标记指针的目录和数据。
    
    
    <label></label> 标签
    
    h5/CSS3:
    
    transition:	// 过渡
    trasform:	// 设置位置
    animation:	// 动画，注意位移的动画得由animation来实现，而不能简单使用transition来实现
    @keyframe name // 配合animation使用，用于设置动画的整个帧状态
    
    
    一份标准的HTML文档有哪几个必须的HTML标签
    1、<html></html>
    2、<head></head>
    3、<title></title>
    4、<body></body>
    
    
    
    HTML5中：
    自关闭的斜线(/)对 ：area, base, br, col, command, embed, hr, img, input, keygen, link, meta, param, source, track, wbr空标签无效，言即不再需要自闭合(/)这个小尾巴了，如果要写上也是可以的。`,
);