<template>
    <div id="TitleZone" ref="root">  
        <br>
        <br>
        <!-- <v-touchable-label text="GSan的电子之海" ></v-touchable-label> -->
        <v-touchable-label text="GSan的个人主页" ></v-touchable-label>
        <router-view name="menubar" ref="menuBar"></router-view>
    </div>
</template>

<script>

import VTouchableLabel from '../view/TouchableLabel.vue';
import VMenuBar from '../view/Menu/MenuBar/MenuBar.vue';
import VSideMenuBar from '../view/Menu/SideMenuBar/SideMenuBar.vue';

let MenuItem = VMenuBar.MenuItem;
let SideMenuItem = VSideMenuBar.SideMenuItem;
let DropDownItem = VMenuBar.DropDownItem;

export default {
    name: "TitleZone",
    data(){
        return {
            titleZoneMinHeight : 10,
        }
    },

    components:{
        VTouchableLabel,
    },

    methods:{
        /// private

        InitPCMenu(){

            let menuBar = this.$refs.menuBar;

            menuBar.AddAnItem(new MenuItem("return", "返回上级", ()=>{ 
                // window.console.trace("Return has been clicked!!!!!!!!!!!!!!!!!!!!!!!!!!!!1");
                this.$emit("onReturn");}, null, true)
            );

            menuBar.AddAnItem(new MenuItem("article", "随笔", ()=>{},
            [
                new DropDownItem("growth", "成长历程", ()=>{ 
                    // this.$emit("onArticle", 1); 
                    // this.$router.push("/article/growth");
                    }),
                new DropDownItem("coderExperience", "个人编程哲学", ()=>{ 
                    // this.$emit("onArticle", 2); 
                    // this.$router.push("/article/code");
                    })
            ]));

            menuBar.AddAnItem(new MenuItem("code", "编程学习", ()=>{},
            [
                new DropDownItem("css", "CSS", ()=>{ 
                    this.$emit("onCode", "CSS"); 
                    this.$router.push("/code/css");
                    }),

                new DropDownItem("html", "HTML", ()=>{ 
                    this.$emit("onCode", "HTML");
                    this.$router.push("/code/html");
                    }),
                
                new DropDownItem("js", "JavaScript", ()=>{ 
                    this.$emit("onCode", "JavaScript"); 
                    this.$router.push("/code/javascript");
                    }),
                
                new DropDownItem("sass", "Sass", ()=>{ 
                    this.$emit("onCode", "Sass"); 
                    this.$router.push("/code/sass");
                    }),
                
                new DropDownItem("vue", "Vue", ()=>{
                    this.$emit("onCode", "Sass"); 
                    this.$router.push("/code/sass");
                    }),

                new DropDownItem("webpack", "Webpack", ()=>{
                    this.$emit("onCode", "Webpack"); 
                    this.$router.push("/code/webpack");
                    }),

                new DropDownItem("regex", "RegEx", ()=>{
                    this.$emit("onCode", "RegEx"); 
                    this.$router.push("/code/regex");
                    }),

                new DropDownItem("browser", "Browser", ()=>{
                    this.$emit("onCode", "Browser"); 
                    this.$router.push("/code/browser");
                    }),

                new DropDownItem("algorithm", "Algorithm", ()=>{
                    this.$emit("onCode", "Algorithm"); 
                    this.$router.push("/code/algorithm");
                    }),

                new DropDownItem("webKnowledge", "WebKnowledge", ()=>{
                    this.$emit("onCode", "WebKnowledge"); 
                    this.$router.push("/code/webknowledge");
                    }),
            ]));
            
            menuBar.AddAnItem(new MenuItem("home", "首页", ()=>{ 
                this.$emit("onHome")
                this.$router.push("/");
                }
            ));

        },

        InitMobileMenu(){

            let menuBar = this.$refs.menuBar;

            menuBar.AddAnItem(new SideMenuItem("home", "首页", ()=>{ 
                this.$emit("onHome")
                this.$router.push("/");
                }
            ));

            menuBar.AddAnItem(new SideMenuItem("code", "编程学习", ()=>{},
            [
                new SideMenuItem("css", "CSS", ()=>{ 
                    this.$emit("onCode", "CSS"); 
                    this.$router.push("/code/css");
                    }),

                new SideMenuItem("html", "HTML", ()=>{ 
                    this.$emit("onCode", "HTML");
                    this.$router.push("/code/html");
                    }),
                
                new SideMenuItem("js", "JavaScript", ()=>{ 
                    this.$emit("onCode", "JavaScript"); 
                    this.$router.push("/code/javascript");
                    }),
                
                new SideMenuItem("sass", "Sass", ()=>{ 
                    this.$emit("onCode", "Sass"); 
                    this.$router.push("/code/sass");
                    }),
                
                new SideMenuItem("vue", "Vue", ()=>{
                    this.$emit("onCode", "Sass"); 
                    this.$router.push("/code/sass");
                    }),

                new SideMenuItem("webpack", "Webpack", ()=>{
                    this.$emit("onCode", "Webpack"); 
                    this.$router.push("/code/webpack");
                    }),

                new SideMenuItem("regex", "RegEx", ()=>{
                    this.$emit("onCode", "RegEx"); 
                    this.$router.push("/code/regex");
                    }),

                new SideMenuItem("browser", "Browser", ()=>{
                    this.$emit("onCode", "Browser"); 
                    this.$router.push("/code/browser");
                    }),

                new SideMenuItem("algorithm", "Algorithm", ()=>{
                    this.$emit("onCode", "Algorithm"); 
                    this.$router.push("/code/algorithm");
                    }),

                new SideMenuItem("webKnowledge", "WebKnowledge", ()=>{
                    this.$emit("onCode", "WebKnowledge"); 
                    this.$router.push("/code/webknowledge");
                    }),
            ]));

            menuBar.AddAnItem(new SideMenuItem("article", "随笔", ()=>{},
            [
                new SideMenuItem("growth", "成长历程", ()=>{ 
                    // this.$emit("onArticle", 1); 
                    // this.$router.push("/article/growth");
                    }),
                new SideMenuItem("coderExperience", "个人编程哲学", ()=>{ 
                    // this.$emit("onArticle", 2); 
                    // this.$router.push("/article/code");
                    })
            ]));

            menuBar.f_onReturnClicked = ()=>{ 
                // window.console.trace("Return has been clicked!!!!!!!!!!!!!!!!!!!!!!!!!!!!1");
                this.$emit("onReturn");
            }

            menuBar.SetCurrentListAsRoot();
        }
    },

    mounted(){

        if(this.$route.query.platform === "pc"){    
            
            this.InitPCMenu();

        }else if(this.$route.query.platform === "mobile"){
           
           this.InitMobileMenu();
        
        }
    },

    OnWindowClick(o_event){
        VMenuBar.OnWindowClick(o_event);
    },

    OnWindowTouchMove(o_touchEvent){
        VSideMenuBar.OnWindowTouchMove(o_touchEvent);
    }
}

</script>


<style lang="scss" scoped>

#TitleZone{
    width: 100%;
    height: 160px;
    background-image:url("../../image/FGO.jpg");
    background-repeat: repeat;
    clip-path: margin-box;
}

</style>