<template>
    <div id="MenuItem" :class="itemStyle" 
        @click="OnClickedItem"
        @pointerdown="OnPointerDownItem"
        @pointerup="OnPointerUpItem"
        ref="menuItem">
        <p ref="menuItemP">{{text}}</p>
        <transition name="dropDownMenu" 
            @leave-cancelled="TransitionLeaveCancelled"
            @after-leave="AfterTransitionLeave"> 
            <ul v-show="ifShowDropDownMenu" ref="dropDownUl">
                <li 
                    :class="dropDownMenuStyle" 
                    v-for="dropDownItem in dropDownMenuItems" 
                    :key="dropDownItem.id"
                    @click.stop="OnClickedDropDownItem(dropDownItem.callback)" 
                    ref="dropDownItems"
                >
                    {{dropDownItem.text}}
                </li>
            </ul>
        </transition>
    </div>
</template>

<script>

class DropDownItem{
    constructor(id, text, callback){
        if(this.ifParamsTypeOK(id, text, callback)){
            this.id = id;
            this.text = text;
            this.callback = callback;
        }else{
            window.console.error("Instantiate DropDownItem error, use default param.");
            return DropDownItem.DEFAULT;
        }
    }

    IfFieldsOK(){
        if(this.ifParamsTypeOK(this.id, this.text, this.callback)){
            if(this.id == "DefaultId"){
                window.console.error("DropDownItem id can't be string 'DefaultId'");
                return false;
            }else {
                return true;
            }
        }else{
            return false;
        }
    }

    ifParamsTypeOK(id, text, callback){
        if(typeof(id) != "string" && typeof id != "number"){
            window.console.error("DropDownItem param id must be string or number.");
            return false;
        }

        if(typeof(text) != "string"){
            window.console.error("DropDownItem param text must be string.");
            return false;
        }

        if(typeof(callback) != "function"){
            window.console.error("DropDownItem param callback must be function.");
            return false;
        }

        return true;
    }
}

DropDownItem.DEFAULT = new DropDownItem("DefaultId", "Default DropDownItem", ()=>{});
Object.defineProperty(DropDownItem.DEFAULT, "id", { writable: false});
Object.defineProperty(DropDownItem.DEFAULT, "text", { writable: false});
Object.defineProperty(DropDownItem.DEFAULT, "callback", { writable: false});

export default {
    name:"MenuItem",

    props:[
        "onclick"
    ],

    data(){
        return {
            /* Common */
            ifSelected: false,
            isDropDownItem: true,
            isButton: false,

            /* MenuItem */
            text:"default",
            callbackFunc:null,
            itemStyle:{
                default: true,
                clicked: false,
            },
            menuItemDOM: null,

            /* DropDownMenu */

            //styles
            dropDownItemTopGaps:10,
            dropDownItemContentHeight:0,
            borderSize:3,

            //datas
            hasInitedVisibility: false,
            ifShowDropDownMenu: true,
            isWaitingAutoClose: false,
            dropDownMenuItems:[
                // for test, but at least left one
                new DropDownItem('1', 'dropDown1', ()=>{window.console.log("DropDownItem1 has been clicked!")}),
                new DropDownItem('2', 'dropDown2', ()=>{window.console.log("DropDownItem2 has been clicked!")}),
            ],
            dropDownMenuStyle:{
                dropDownMenuItemType1: true,
                dropDownMenuItemType2: false,
            }
        }
    },

    methods:{

        // setter & getter
        SetText(text){
            this.text = text;
        },

        SetClickedCallBack(callbackFunc){
            this.callbackFunc = callbackFunc;
        },

        SetIsDropDonItem(isDropDownItem){
            this.isDropDownItem = isDropDownItem;
            if(!this.isDropDownItem && this.dropDownMenuItems.length > 0){
                this.dropDownMenuItems = [];
            }
        },

        SetIsButton(isButton){
            this.isButton = isButton;
        },

        SetDropDownItemsByList(dropDownItemList){
            if(dropDownItemList){
                if(Array.isArray(dropDownItemList)){

                    // if want to check again, use this.
                    // for(let dropDownItem of this.dropDownItemList){
                    //     if(!dropDownItem.IfFieldsOK()){
                    //         return;
                    //     }
                    // }

                    this.dropDownMenuItems = dropDownItemList;
                    this.isDropDownItem = true;
                    this.isButton = false;
                }else{
                    window.console.error("MenuItem SetDropDownItemsByList must input an array.");
                }
            }else{
                this.dropDownMenuItems = [];
                this.isDropDownItem = false;
            }
        },

        SetDropDownItemsPositionAuto(){
            let dropDownItems = this.$refs.dropDownItems;
            if(dropDownItems.length > 0){
                this.dropDownItemContentHeight = dropDownItems[0].offsetHeight;
                let index = 0;
                for(let dropDownItem of dropDownItems){
                    dropDownItem.style.top = (this.dropDownItemContentHeight + this.dropDownItemTopGaps) * index + this.dropDownItemTopGaps + "px";     
                    ++index;
                }
            }
        },

        // events

        OnClickedItem(e){
            if(!this.isButton){
                this.$emit("onclicked", e, this.id)
            }
        },

        OnClickedDropDownItem(callback){
            if(callback){
                callback();
            }
            this.$emit("clearAllSelectionState");
        },

        OnPointerDownItem(){
            if(this.isButton){
                this.itemStyle.default = false;
                this.itemStyle.clicked = true;
            }
        },

        OnPointerUpItem(){
            if(this.isButton){
                this.itemStyle.default = true;
                this.itemStyle.clicked = false;
                if(this.callbackFunc){
                    this.callbackFunc();
                }
                this.$emit("clearAllSelectionState");
            }
        },

        AfterTransitionLeave(){
            if(!this.hasInitedVisibility){
                this.$refs.dropDownUl.style["visibility"] = "visible";
                this.hasInitedVisibility = true;
            }

            if(this.isWaitingAutoClose){
                this.$emit("onAutoCloseDropDownMenuFinished", this.id)
                this.isWaitingAutoClose = false;
            }
        },

        TransitionLeaveCancelled(){
            if(!this.hasInitedVisibility){
                this.$refs.dropDownUl.style["visibility"] = "visible";
                this.hasInitedVisibility = true;
            }
        },

        // behaviours

        Focus(isAuto = false){

            if(!this.isButton){
            
                this.ifSelected = true;

                this.itemStyle.default = false;
                this.itemStyle.clicked = true;

                if(this.isDropDownItem && !this.isButton){
                    this.ifShowDropDownMenu = true;
                }else{
                    if(this.callbackFunc){
                        this.callbackFunc();
                    }
                }

                if(isAuto){
                    // do something
                }
            }
        },

        Defocus(isAuto = false){

            if(!this.isButton){

                this.ifSelected = false;

                this.itemStyle.default = true;
                this.itemStyle.clicked = false;

                if(this.isDropDownItem){
                    this.ifShowDropDownMenu = false;
                }

                if(isAuto){
                    this.isWaitingAutoClose = true;
                }
            }
        },

    },

    mounted(){
        this.ifShowDropDownMenu = false;
        this.SetDropDownItemsPositionAuto();
        this.menuItemDOM = this.$refs.menuItem;
        this.$emit("onMenuItemMounted", this.menuItemDOM);
    },

    watch:{
        dropDownMenuItems(){
            this.$nextTick(()=>{
                this.SetDropDownItemsPositionAuto();
            });
        }
    },

    destroyed(){
        this.$emit("onMenuItemDesdroyed", this.menuItemDOM);
    },

    /* other */
    DropDownItem
}

</script>

<style lang="scss" scoped>

.base{
    margin-left: 20px;
    margin-right: 20px;
    position: relative;
    z-index: 10;
}

.baseP{
    margin: 0px;
    border-radius: 20%;
    font-size: 20px;
    text-align: center;
    -moz-user-select:none;/*火狐*/
    -webkit-user-select:none;/*webkit浏览器*/
    -ms-user-select:none;/*IE10*/
    -khtml-user-select:none;/*早期浏览器*/
    pointer-events: none;
    user-select:none;
}

.baseUL{
    margin: 0px;
    padding-inline-start: 0;
    // top: 20px;
    position: absolute;
    visibility: hidden;
    // transform: translate(0,0);
}

.default{
    @extend .base;
    p{
        @extend .baseP;
        color: black;
        border: 3px solid black;
        background-color: white;
    }
    ul{
        @extend .baseUL;
    }
}

.clicked{
    @extend .base;
    p{
        @extend .baseP;
        color: white;
        border: 3px solid rgb(231, 217, 217);
        background-color: black;
    }
    ul{
        @extend .baseUL;
    }
}

.dropDownMenuBase{
    top: 20px;
    list-style: none;
    font-size: 10px;
    text-align: center;
    width: 100px;
    position: absolute;
    &:hover {
        background-color: red;
    }
    -o-user-select: none;
    -moz-user-select: none; /*火狐 firefox*/
    -webkit-user-select: none; /*webkit浏览器*/
    -ms-user-select: none; /*IE10+*/
    -khtml-user-select :none; /*早期的浏览器*/
    user-select: none; 
}

.dropDownMenuItemType1{
    @extend .dropDownMenuBase;
    
    color: white;
    background-color: black;
    border: 3px gray solid;
}

.dropDownMenuItemType2{
    @extend .dropDownMenuBase;
    color: black;
    background-color: white;
    border: 3px black solid;
}

//vue animations

.dropDownMenu-active-base{
    pointer-events: none;
}

.dropDownMenu-enter-active{
    animation: 0.5s scaleIn;
    @extend .dropDownMenu-active-base;
}

.dropDownMenu-leave-active{
    animation: 0.5s scaleOut;
    @extend .dropDownMenu-active-base;
}

@keyframes scaleIn {
    from { transform: scaleY(0); }
    20% { transform: scaleY(1.2); }
    to   { transform: scaleY(1); }
}

@keyframes scaleOut {
    from { transform: scaleY(1); }
    80% { transform: scaleY(1.2); }
    to   { transform: scaleY(0); }
}

</style>





