<template>
    
    <div>
        <v-news-style-box
            v-for="o_newsBox in o_newsBoxes"
            :key="o_newsBox.id"
            @onNewsStyleBoxMounted="OnNewsStyleBoxMounted"
            @onBoxEnterDone="onBoxEnterDone"
            @onBoxLeaveDone="OnBoxLeaveDone"
            @onBoxContentBeforeEnter="OnBoxContentBeforeEnter"
            @onBoxContentEnterDone="OnBoxContentEnterDone"
            @onBoxContentLeaveDone="OnBoxContentLeaveDone"
            @onSelectABox="OnSelectABox"
            ref="boxes"
        >
        </v-news-style-box>
    </div>

</template>

<script>

import VNewsStyleBox from "./NewsStyleBox.vue";
import {Data} from "../../../data/Data.js";    // 如果之后要剥离该组件就不在这里导入Data了

let NewsStyleBox = VNewsStyleBox.NewsStyleBox;
let o_windowResizeEventMap = new Map();
let o_historyPath = new Map();

class HistoryData{
    constructor(s_rootTypeNow){
        this.s_rootTypeNow = s_rootTypeNow;
        this.o_baseData = [];
    }
}

export default {

    props:{
        placeID:{
            "default":1,
            "required":true,
        }
    },

    data(){
        return{
            /// public
            o_parent: null,

            /// private
            o_newsBoxes:[                               // 只是预设了一些测试数据不要在意
                new NewsStyleBox(1,"test1", "xixi"), 
                new NewsStyleBox(2,"test2", "xixi2"), 
                new NewsStyleBox(3,"test3", "xixi3"), 
                new NewsStyleBox(4,"test4", "xixi4"), 
                new NewsStyleBox(5,"test5", "xixi5"), 
                new NewsStyleBox(6,"test6", "xixi6"), 
                new NewsStyleBox(7,"test7", "xixi7"), 
                new NewsStyleBox(8,"test8", "xixi8"), 
                new NewsStyleBox(9,"test9", "xixi9"), 
                new NewsStyleBox(10,"test10", "xixi10"), 
            ],

            o_selectedTargetBox: null,
            n_hasEnteredBoxNumer: 0,
            n_hasLeftBoxNumber: 0,

            f_onBoxEnterDoneCallback: null,
            f_onAllBoxDisappearCallback: null,
        }
    },

    methods:{

        /// getter & setter
        SetNewsBoxDataByIndex(newsStyleBox, index = -1){
            this.$nextTick(()=>{
                if(this.o_newsBoxes.length > 0){
                    if(index == -1){
                        let index = this.o_newsBoxes.length - 1;
                        let newsBox = this.$refs.boxes[index];
                        newsBox.SetData(newsStyleBox);
                    }else{
                        let newsBox = this.$refs.boxes[index];
                        if(newsBox){
                            newsBox.SetData(newsStyleBox);
                        }
                    }
                }
            });
        },

        GetParentWidth(){
            return this.o_parent.$refs.middleZone.offsetWidth;
        },

        /// public

        // 可以传入数据类型为BaseDirectory的对象进行数组更新, 注意是延迟更新，有过渡动画
        RefreshAllBoxWithTrasition(o_directory = null, forceRightNow=false){

            this.f_onAllBoxDisappearCallback = ()=>{   
                if(o_directory){
                    this.o_newsBoxes = [];
                    for(let i=0; i<o_directory.o_children.length; ++i){
                        let child = o_directory.o_children[i];
                        if(child.b_isDirectory){
                            this.o_newsBoxes.push(new NewsStyleBox(i, child.s_title, child.s_description, "", child));
                        }else{
                            this.o_newsBoxes.push(new NewsStyleBox(i, child.s_title, child.s_description, child.s_document));
                        }
                    }
                }

                let len =  this.o_newsBoxes.length;
                for(let i=0; i<len; ++i){
                    this.SetNewsBoxDataByIndex(this.o_newsBoxes[i], i);
                }

                this.$nextTick(()=>{
                    for(let newsBox of this.$refs.boxes){
                        newsBox.b_ifShow = true;
                    }
                });
            }

            if(this.$refs.boxes.length > 0 && !forceRightNow){
                for(let newsBox of this.$refs.boxes){
                    newsBox.b_ifShow = false;
                    newsBox.b_isShowLabel = true;
                    newsBox.b_isShowDocument = false;
                }
                this.n_hasLeftBoxNumber = 0;
            }else{
                this.f_onAllBoxDisappearCallback();
                this.f_onAllBoxDisappearCallback = null;
            }
        },

        OnReturn(){
            let historyData = o_historyPath.get(this.placeID);
            if(historyData){
                if(this.o_selectedTargetBox){
                    this.OnSelectABox(this.o_selectedTargetBox);
                    this.f_onBoxEnterDoneCallback = ()=>{
                        // window.console.log(historyData.o_baseData);
                        // 从选中状态下返回不pop
                        this.RefreshAllBoxWithTrasition(historyData.o_baseData[historyData.o_baseData.length-1],true);
                    };
                }else{
                    window.console.log(historyData.o_baseData);
                    if(historyData.o_baseData.length > 1){
                        historyData.o_baseData.pop();
                    }
                    this.RefreshAllBoxWithTrasition(historyData.o_baseData[historyData.o_baseData.length-1]);
                }
            }
        },

        /// event

        OnNewsStyleBoxMounted(o_newsStyleBox){
            o_newsStyleBox.o_parent = this;
        },

        onBoxEnterDone(){
            if(this.f_onBoxEnterDoneCallback){
                this.f_onBoxEnterDoneCallback();
                this.f_onBoxEnterDoneCallback = null;
            }
        },

        OnBoxLeaveDone(){
            ++this.n_hasLeftBoxNumber;
            if(this.f_onAllBoxDisappearCallback){
                if(this.n_hasLeftBoxNumber == this.$refs.boxes.length){
                    this.f_onAllBoxDisappearCallback();
                    this.f_onAllBoxDisappearCallback = null;
                }
            }else{
                if(this.n_hasLeftBoxNumber == this.$refs.boxes.length-1){
                    this.o_selectedTargetBox.b_isShowLabel = false;
                    this.o_selectedTargetBox.b_isShowDocument = true;
                }
            }
        },

        OnBoxContentBeforeEnter(){
            this.$emit("onNewsStyleBoxContainerContentChanged");
        },

        OnBoxContentEnterDone(){
            this.$emit("onNewsStyleBoxContainerContentChanged");
        },

        OnBoxContentLeaveDone(){
            if(this.o_selectedTargetBox && !this.o_selectedTargetBox.b_isShowDocument){
                for(let newsBox of this.$refs.boxes){
                    newsBox.b_ifShow = true;
                    newsBox.b_isShowLabel = true;
                    newsBox.b_isShowDocument = false;
                }
                this.o_selectedTargetBox = null;
            }
        },

        OnSelectABox(o_newsBox){
            if(o_newsBox.o_dataNode){
                // TODO: 这里可以做目录层级的切换动画
                this.RefreshAllBoxWithTrasition(o_newsBox.o_dataNode);
                window.console.log(o_newsBox.o_dataNode);
                let historyPathArray = o_historyPath.get(this.placeID);
                historyPathArray.o_baseData.push(o_newsBox.o_dataNode);
            }else{
                if(this.$refs.boxes.length > 1){
                    if(!o_newsBox.b_isShowDocument){
                        for(let newsBox of this.$refs.boxes){
                            if(newsBox.n_id != o_newsBox.n_id){
                                newsBox.b_ifShow = false;
                                newsBox.b_isShowLabel = true;
                                newsBox.b_isShowDocument = false;
                            }
                        }
                        this.n_hasLeftBoxNumber = 0;
                        this.o_selectedTargetBox = o_newsBox;
                    }else{
                        this.o_selectedTargetBox = o_newsBox;
                        this.o_selectedTargetBox.b_isShowLabel = true;
                        this.o_selectedTargetBox.b_isShowDocument = false;
                    }
                }
                else{
                    this.o_selectedTargetBox = o_newsBox;
                    if(!o_newsBox.b_isShowDocument){
                        this.o_selectedTargetBox.b_isShowLabel = false;
                        this.o_selectedTargetBox.b_isShowDocument = true;
                    }else{
                        this.o_selectedTargetBox.b_isShowLabel = true;
                        this.o_selectedTargetBox.b_isShowDocument = false;
                        this.o_selectedTargetBox = null;
                    }
                }
            }
        },

        OnWindowResize(){
            //TODO: may be need
            // window.console.log("NewsStyleBoxContainer resize");
        },

        /// private

        refreshBoxesByHistory(s_fromRootType,s_toRootType,s_fromType,s_toType){
            let historyData = o_historyPath.get(this.placeID);
            
            if(!historyData){
                o_historyPath.set(this.placeID, new HistoryData(s_toRootType));
                historyData = o_historyPath.get(this.placeID);
            }

            if(s_fromType !== s_toType){
                
                if(s_fromRootType !== s_toRootType){

                    historyData.s_rootTypeNow = s_toRootType;
                }

                historyData.o_baseData = [];
                
                let dataRoot = Data[historyData.s_rootTypeNow];
                
                // window.console.log(dataRoot);
                // window.console.log(s_toRootType);
                // window.console.log(dataRoot.o_children[s_toType]);
                
                historyData.o_baseData.push(dataRoot.o_children[s_toType]);
            }

            this.RefreshAllBoxWithTrasition(historyData.o_baseData[historyData.o_baseData.length-1]);
        },
    },

    components:{
        VNewsStyleBox,
    },

    mounted(){
        o_windowResizeEventMap.set(this,this.OnWindowResize);
    },

    updated(){
        this.$emit("onNewsStyleBoxContainerContentChanged");
    },

    destroyed(){
        o_windowResizeEventMap.delete(this);
        this.o_selectedTargetBox = null;
        this.o_newsBoxes = null;
    },

    beforeRouteEnter(to, from, next){
        next(vm=>{
            if(vm.o_selectedTargetBox){
                vm.OnSelectABox(vm.o_selectedTargetBox);
                vm.f_onBoxEnterDoneCallback = ()=>{
                    vm.refreshBoxesByHistory(from.params.rootType,to.params.rootType,from.params.type,to.params.type);
                    vm.$emit("onRouterComBeMounted", vm);
                };
            }else{
                vm.refreshBoxesByHistory(from.params.rootType,to.params.rootType,from.params.type,to.params.type);
                vm.$emit("onRouterComBeMounted", vm);
            }
        });
    },

    beforeRouteUpdate(to, from, next){
        // window.console.log("beforeRouteUpdate has been called");
        // window.console.log(this.o_selectedTargetBox);
        if(this.o_selectedTargetBox){
            this.OnSelectABox(this.o_selectedTargetBox);
            this.f_onBoxEnterDoneCallback = ()=>{
                this.refreshBoxesByHistory(from.params.rootType,to.params.rootType,from.params.type,to.params.type);
                next();
            };
        }else{
            this.refreshBoxesByHistory(from.params.rootType,to.params.rootType,from.params.type,to.params.type);
            next();
        }
    },

    beforeRouteLeave(to, from, next){
        if(this.o_selectedTargetBox){
            this.OnSelectABox(this.o_selectedTargetBox);
            this.f_onBoxEnterDoneCallback = ()=>{
                //TODO: 消失动画
                next();
            };
        }else{
            //TODO: 消失动画
            next();
        }
    },

    OnWindowResize(){
        let keys = o_windowResizeEventMap.keys();
        let key = keys.next().value;
        while(key){
            o_windowResizeEventMap.get(key).apply(key);
            key = keys.next().value;
        }
        VNewsStyleBox.OnWindowResize();
    },

    NewsStyleBox
}
</script>

<style lang="scss" scoped>

</style>