import Vue from 'vue';
import VueRouter from 'vue-router';
import commonTools from './commonTools.js';

/// 该文件专门用来匹配路由和组件的关系

let originalReplace = VueRouter.prototype.replace;
VueRouter.prototype.replace = function replace(location, onResolve, onReject) {
  if (onResolve || onReject) return originalReplace.call(this, location, onResolve, onReject)
  return originalReplace.call(this, location).catch(err => err)
};
VueRouter.prototype.push = function push(location, onResolve, onReject) {
  if (onResolve || onReject) return originalReplace.call(this, location, onResolve, onReject)
  return originalReplace.call(this, location).catch(err => err)
};

Vue.use(VueRouter);

/// 引入外部需要设置的所有components
import VHome from "../components/view/Home.vue";
import VPageNotFound from "../components/view/404.vue";
import VNewsStyleBoxContainer from "../components/view/NewsStyleBox/NewsStyleBoxContainer.vue";
import VMenuBar from "../components/view/Menu/MenuBar/MenuBar.vue";
import VSideMenuBar from "../components/view/Menu/SideMenuBar/SideMenuBar.vue";

//全局Router
const router = new VueRouter({
  // mode:"history",
  routes: [
    
    { path: "*" , component: VPageNotFound },

    {   
        path: "/pc/home", 
        
        name:"PCHome", 
        
        components:{
            
            "content": VHome,

            "menubar": VMenuBar,
        }
    },
    
    {
        path: "/pc/:rootType/:type",
        
        name:"PCContent",
        
        components:{
        
          "content": VNewsStyleBoxContainer,
        
          "menubar": VMenuBar,
          
        }  
    },
        
    {
        path: "/mobile/home",

        name: "MobileHome",
          
        components:{
            
            "content": VHome,

            "menubar": VSideMenuBar,      
        }
    },
              
    {
        path:"/mobile/:rootType/:type",

        name:"MobileContent",

        components:{
 
            "content": VNewsStyleBoxContainer,
 
            "menubar": VSideMenuBar,
        }  
    },

  ]

});

let s_platform = null;

if(commonTools.isRunningOnPC){
  router.rootPath = "/pc";
  s_platform = "pc";
  router.addRoutes([
    { path: "/", redirect:"/pc/home" },
  ]);
}else{
  router.rootPath = "/mobile";
  s_platform = "mobile";
  router.addRoutes([
    { path: "/", redirect:"/mobile/home"},
  ]);
}

// 改写push和replace函数，添加根目录

let f_push = router.push;
router.push = function push(location, onComplete, onAbort){
  if(location === '/'){
    location = "/home";
  }
  location = router.rootPath + location + "?platform=" + s_platform;
  // window.console.trace("location is "+location);
  f_push.call(router, location, onComplete, onAbort);
}

// window.console.log(Object.getOwnPropertyDescriptor(router, "push"));

let f_replace = router.replace;
router.replace = function replace(location, onComplete, onAbort){
  if(location === '/'){
    location = "/home";
  }
  location = router.rootPath + location + "?platform=" + s_platform;
  f_replace.call(router, location, onComplete, onAbort);
}

export default router;

// window.console.log(router);