 <template>

    <div v-cloak id="TitleBox" 
        :class="titleStyle" 
        @mouseover.stop.prevent="MouseOverTitle" 
        @mousemove.stop.prevent="MouseMoveTitle"
        @mouseout.stop.prevent="MouseOutTitle"
        ref="titleBox"
        >
        <div id="TitleContent" ref="titleContent">{{text}}</div>
    </div>

 </template>

<script>
export default {
    name: "TouchableLabel",
    props:{
        text: {
            type: String,
            default: "LabelText",
        },
    },
    data(){
        return {
            titleStyle: {
                "Title_MouseOut" : true,
                "Title_MouseOver" : false
            },  

            affectRate: 0.1,
            moveSpeed: 1,   //px
            defaultPosition: {x:0, y:0},
            targetPosition: {x:0, y:0},
            speedUnitVector: {x:0, y:0},
            halfContentRect: {w:0, h:0},
            clock: null
        }
    },
    mounted(){

        //计算初始位置的left
        var halfTitleBoxWidth = this.$refs.titleBox.offsetWidth / 2;
        this.halfContentRect.w = this.$refs.titleContent.offsetWidth / 2;
        var contentPosX = this.$refs.titleContent.offsetLeft + halfTitleBoxWidth - this.halfContentRect.w;
        
        //计算初始位置的Top
        var halfTitleBoxHeight = this.$refs.titleBox.offsetHeight / 2;
        this.halfContentRect.h = this.$refs.titleContent.offsetHeight / 2;
        var contentPosY = this.$refs.titleContent.offsetTop + halfTitleBoxHeight - this.halfContentRect.h;

        //储存默认位置
        this.defaultPosition.x =  parseInt(contentPosX);
        this.defaultPosition.y = parseInt(contentPosY);

        //设置新的位置
        this.$refs.titleContent.style.left = this.defaultPosition.x + "px";
        this.$refs.titleContent.style.top = this.defaultPosition.y + "px";
    },

    methods:{
        RefreshTargetPosition(e){

            var deltaX = e.clientX - this.defaultPosition.x - this.halfContentRect.w;
            var deltaY = e.clientY - this.defaultPosition.y - this.halfContentRect.h;

            var vectorX = deltaX * this.affectRate;
            var vectorY = deltaY * this.affectRate;

            this.targetPosition.x = parseInt(this.defaultPosition.x + vectorX);
            this.targetPosition.y = parseInt(this.defaultPosition.y + vectorY);
            
            var left = parseInt(this.$refs.titleContent.offsetLeft);
            var top = parseInt(this.$refs.titleContent.offsetTop);

            deltaX = this.targetPosition.x - left;
            deltaY = this.targetPosition.y - top;

            var len = Math.sqrt(Math.pow(deltaX,2) + Math.pow(deltaY,2));

            //更新方向向量
            this.speedUnitVector.x = (deltaX / len) * this.moveSpeed;
            this.speedUnitVector.y = (deltaY / len) * this.moveSpeed;
        },

        CalNextValueBySpeed(curValue, targetValue, speed, easeType="defaultEase"){
            var newValue;
            if(easeType == "defaultEase"){            
                var increase = parseInt(Math.abs( curValue - targetValue) / 100 * speed);
                if(increase == 0){
                    if(Math.abs(speed) < 0.1){
                        return targetValue;
                    }else if(speed < 0){
                        increase = -1;
                    }else{
                        increase = 1;
                    }
                }
                newValue = curValue + increase;
            }
            return newValue;
        },

        MouseOverTitle(e){

            this.titleStyle.Title_MouseOver = true;
            this.titleStyle.Title_MouseOut = false;

            this.RefreshTargetPosition(e);
            
            this.clock = setInterval(()=>{
                
                //实现向目标位置移动
                var left = parseInt(this.$refs.titleContent.offsetLeft);
                var top = parseInt(this.$refs.titleContent.offsetTop);

                if(left != this.targetPosition.x){
                    var newX = this.CalNextValueBySpeed(left, this.targetPosition.x, this.speedUnitVector.x);
                    this.$refs.titleContent.style.left = parseInt(newX) + "px";
                }

                if(top != this.targetPosition.y){
                    var newY = this.CalNextValueBySpeed(top, this.targetPosition.y, this.speedUnitVector.y);
                    this.$refs.titleContent.style.top = parseInt(newY) + "px";
                }

            }, 10);

        },
        
        MouseMoveTitle(e){
            this.RefreshTargetPosition(e);
        },

        MouseOutTitle(){

            this.titleStyle.Title_MouseOver = false;
            this.titleStyle.Title_MouseOut = true;

            if(this.clock){
                clearInterval(this.clock);
                this.clock = null;
            }

            this.$nextTick( () => { //恢复默认位置
                this.$refs.titleContent.style.left = this.defaultPosition.x + "px";
                this.$refs.titleContent.style.top = this.defaultPosition.y + "px";
            });

        }
    }
}
</script>

<style lang="scss" scoped>

    .TitleMouseBaseSytle {
        width: 400px;
        height: 60px;
        margin-left: 30px;
        border: 5px rgb(121, 204, 224) outset;
        transition: all 0.6s ease;
    }

    .Title_MouseOver {
        color: #74a5e6;
        background-color: rgb(101, 123, 124);
        @extend .TitleMouseBaseSytle;
    }

    .Title_MouseOut{
        color: #7f72c9;
        background-color: rgb(209, 207, 197);
        @extend .TitleMouseBaseSytle;
    }

    #TitleContent{
        font-size: 30px;
        font-family: Verdana, Geneva, Tahoma, sans-serif;
        font-style: italic;
        position: absolute;
        -moz-user-select:none;/*火狐*/
        -webkit-user-select:none;/*webkit浏览器*/
        -ms-user-select:none;/*IE10*/
        -khtml-user-select:none;/*早期浏览器*/
        pointer-events: none;
    }

</style>