import {BaseData} from "../../../base/BaseData.js";

export let MustacheGrammer = new BaseData(
    "Mustache语法",
    "Mustache的语法记录",
    `mustache的模板语法：
	{{data}}
 	{{#data}}{{/data}}
 	{{^data}}{{/data}}
 	{{.}}
 	{<partials}}
 	{{{data}}}
 	{{!comments}}

借助 Demo 来对语法做简单的介绍：
...
<script type="text/javascript" src="mustache.js"></script>
<script type="text/javascript">
var data = {
    "name": " xiaohua ",
    "msg": {
        "sex": " female ", 
        "age": " 22 ", 
        "hobit": " reading "
    },
    "subject": ["Ch","En","Math","physics"] 
  }  

  var tpl = '<p> {{name}}</p>'; 
  var html = Mustache.render(tpl, data);

  alert(html);
</script>
...

1.
{{data}}
{就是 Mustache 的标示符，花括号里的 data 表示键名，这句的作用是直接输出与键名匹配的键值，例如：

 var tpl = '{{name}}';
 var html = Mustache.render(tpl, data);
 //输出：
 xiaohua

2.
{{#data}} {{/data}}
以#开始、以/结束表示区块，它会根据当前上下文中的键值来对区块进行一次或多次渲染，例如改写下 Demo 中的 tpl：

var tpl = '{{#msg}} <p>{{sex}},{{age}},{{hobit}}</p> {{/msg}}';
var html = Mustache.render(tpl, data);

//输出：
<p> female, 22, reading</p>
注意：如果{ {{/data}}中的 data 值为 null, undefined, false；则不渲染输出任何内容。

3.
{{^data}} {{/data}}
该语法与{ {{/data}}类似，不同在于它是当 data值为 null, undefined, false 时才渲染输出该区块内容。

 var tpl = {{^nothing}}没找到 nothing 键名就会渲染这段{{/nothing}};
 var html = Mustache.render(tpl, data);
 //输出：
 没找到 nothing 键名就会渲染这段

4.
{{.}}
{{.}}表示枚举，可以循环输出整个数组，例如：

 var tpl = '{{#subject}} <p>{{.}}</p> {{/subject}}';
 var html = Mustache.render(tpl, data);
 //输出：
 <p>Ch</p> <p>En</p> <p>Math</p> <p>physics</p>

5.
{{>partials}}
以>开始表示子模块，如{{> msg}}；当结构比较复杂时，我们可以使用该语法将复杂的结构拆分成几个小的子模块，例如：

var tpl = "<h1>{{namme}}</h1> <ul>{{>msg}}</ul>"
 var partials = {msg: "{{#msg}}<li>{{sex}}</li><li>{{age}}</li><li>{{hobit}}</li>{{/msg}}"}
 var html = Mustache.render(tpl, data, partials);
 //输出：
 <h1>xiaohua</h1>
 <ul>
    <li>female</li>
    <li>22</li>
    <li>reading</li>
</ul>

6.
{{{data}}}
{{data}}输出会将等特殊字符转译，如果想保持内容原样输出可以使用{{，例如：

 var tpl = '{{#msg}} <p>{{{age}}}</p> {{/msg}}'
 //输出：
 <p>22</p>

7.
{{ !comments}}
!表示注释，注释后不会渲染输出任何内容。

{{ !这里是注释}}
//输出：`,
);