<template>
    <transition 
        name="rootTransition"
        @after-enter="OnRootEnterDone"
        @after-leave="OnRootLeaveDone"
        appear
        >
        <div :class="o_baseStyle" 
            @pointerenter="onPointerEnter"
            @pointerdown="onPointerDown"
            @pointermove="onPointerMove"
            @pointerleave="onPointerLeave"
            @pointerup="onPointerUp"
            v-show="b_ifShow"
            ref="root"
        >
            <img src="../../../image/fgo_head.png" class="headImgStyle" ref="headImg"/>
            <transition-group 
                name="contentChange" 
                mode="out-in"
                tag=""
                @before-enter="OnContentBeforeEnter"
                @after-enter="OnContentEnterDone"
                @after-leave="OnContentLeaveDone"
                >
                <div v-if="b_isShowLabel" key="label">
                    <h1>{{s_title}}</h1>
                    <p class="document_newsBox" ref="text">{{s_content}}</p>
                </div>
                <div v-if="b_isShowDocument" key="document">
                    <p class="document_open" ref="document"> {{s_document}} </p>
                </div>
            </transition-group>
        </div>
    </transition>
</template>

<script>

class NewsStyleBox{
    constructor(n_id, s_title, s_content, s_document, o_dataNode = null){
        this.n_id = n_id;
        this.s_title = s_title;
        this.s_content = "\t"+s_content;
        this.s_document = s_document;
        this.o_dataNode = o_dataNode;
    }
}

let o_windowResizeEventMap = new Map();

export default {
    name:"NewsStyleBox",
    data(){
        return {

            /// public
            n_id: "-1", 
            s_title: "test:",
            s_content: "yyy",
            s_document: "",
            o_dataNode: null,
            o_parent: null,

            o_baseStyle:{
                defocusbase: true,
                focusBase: false,
                defocusDocument: false,
                // focusDocumentBase: false, 先弃用，效果不好
                focusSelecting: false,
            },

            o_animateStyle:[
                "bounce",
                "pulse",
                "rubberBand",
                "tada",
                "jello",
                "heartBeat"
            ],

            b_ifShow: true,
            b_isShowLabel: true,
            b_isShowDocument: false,

            /// private
            b_ifSelecting: false,

            /// headImg
            f_headImgMinWaitTime: 10,
            f_headImgMaxWaitTime: 15,
            f_animation_once_time: 1,
            f_headImgWaitTimeNow: 0,
            f_headImgWholeWaitTime: 0,
            o_headImgAnimClock: null,
        }
    },

    methods:{

        /// public
        Set2DefaultStyle(){
            this.o_baseStyle.defocusbase = true;
            this.o_baseStyle.defocusDocument = false;    
            this.o_baseStyle.focusBase = false;
            this.o_baseStyle.focusSelecting = false;
            // this.o_baseStyle.focusDocumentBase = false;
        },

        /// getter & setter
        
        getRandomHeadAnimWaitTime(){
            return this.f_headImgMinWaitTime + Math.random() * this.f_headImgMaxWaitTime;
        },

        SetData(o_newsStyleBox){
            this.n_id = o_newsStyleBox.n_id;
            this.s_title = o_newsStyleBox.s_title;
            this.s_content = o_newsStyleBox.s_content;
            this.s_document = o_newsStyleBox.s_document;
            this.o_dataNode = o_newsStyleBox.o_dataNode;
        },

        /// events

        onPointerEnter(){
            if(!this.b_isShowDocument){

                this.o_baseStyle.defocusbase = false;

                this.o_baseStyle.focusBase = true;
            }
        },

        onPointerDown(){
            if(!this.b_isShowDocument){
                
                this.b_ifSelecting = true;
                
                this.o_baseStyle.focusSelecting = true;
            }
        },

        onPointerMove(){
            if(!this.b_isShowDocument){
                
                this.o_baseStyle.defocusbase = false;
                
                this.o_baseStyle.focusBase = true;

                if(this.b_ifSelecting){
                    this.o_baseStyle.focusSelecting = true;
                }else{
                    this.o_baseStyle.focusSelecting = false;
                }
            }
        },

        onPointerLeave(){
            if(!this.b_isShowDocument){

                this.b_ifSelecting = false;
           
                this.o_baseStyle.defocusbase = true;
            
                this.o_baseStyle.defocusDocument = false;

                this.o_baseStyle.focusBase = false;

                this.o_baseStyle.focusSelecting = false;
            }
        },

        onPointerUp(){
            if(!this.b_isShowDocument){

                if(this.b_ifSelecting){
                    // this.b_isShowLabel = !this.b_isShowLabel;
                    this.b_ifSelecting = false;
                
                    this.o_baseStyle.focusSelecting = false;
                
                    this.$emit("onSelectABox", this);
                    // window.console.log("NewsStyleBox has been clicked!!");
                }
            }
        },

        OnRootEnterDone(){
            this.$emit("onBoxEnterDone", this);
        },

        OnRootLeaveDone(){
            this.onPointerLeave();
            this.$emit("onBoxLeaveDone", this);
        },

        OnContentBeforeEnter(){

            this.o_baseStyle.defocusDocument = true;

            this.$emit("onBoxContentBeforeEnter",this);
        },

        OnContentEnterDone(){

            this.$nextTick(()=>{
                if(this.b_isShowDocument){

                    this.$refs.document.style.width = this.o_parent.GetParentWidth() + "px";

                }
            });

            this.$emit("onBoxContentEnterDone",this);
        },

        OnContentLeaveDone(){
            this.onPointerLeave();
            this.$emit("onBoxContentLeaveDone",this);
        },

        OnWindowResize(){
            this.$nextTick(()=>{
                if(this.b_isShowDocument){
                    this.$refs.document.style.width = this.o_parent.GetParentWidth() + "px";
                }
            });
        },

        /// private
        
        ActivateHeadAutoAnim(){
            this.f_headImgWaitTimeNow += 0.01;
            if(this.f_headImgWaitTimeNow >= this.f_headImgWholeWaitTime){
                let index = parseInt((this.o_animateStyle.length-1) * Math.random());
                this.$refs.headImg.style.animation = this.f_animation_once_time + "s " + this.o_animateStyle[index];
                this.f_headImgWholeWaitTime = this.getRandomHeadAnimWaitTime();
                this.f_headImgWaitTimeNow = 0;
            }
        }
    },

    mounted(){

        o_windowResizeEventMap.set(this, this.OnWindowResize);

        let word = "\t";
        for(let i=0; i< 500; ++i){
            word += "i";
        }
        this.s_content = word;
        this.f_headImgWholeWaitTime = this.getRandomHeadAnimWaitTime();
        this.o_headImgAnimClock = window.setInterval(()=>{
            this.ActivateHeadAutoAnim();
        }, 0.01);
        
        this.$emit("onNewsStyleBoxMounted",this);
    },
    
    updated(){
        if(!this.b_ifShow){
            if(this.o_headImgAnimClock){
                window.clearInterval(this.o_headImgAnimClock);
                this.o_headImgAnimClock = null;
                this.f_headImgWaitTimeNow = 0;
                this.$refs.headImg.style.animation = "none";
            }
        }else{
            if(!this.o_headImgAnimClock){
                this.o_headImgAnimClock = window.setInterval(()=>{
                    this.ActivateHeadAutoAnim();
                }, 0.01);
            }
        }
    },

    destroyed(){
        window.clearInterval(this.o_headImgAnimClock);
        this.o_headImgAnimClock = null;
        o_windowResizeEventMap.delete(this);
    },

    OnWindowResize(){
        let keys = o_windowResizeEventMap.keys();
        let key = keys.next().value;
        while(key){
            o_windowResizeEventMap.get(key).apply(key);
            key = keys.next().value;
        }
    },

    NewsStyleBox,
}

</script>

<style lang="scss" scoped>

.userNoSelect{
    -webkit-user-select: none;
    -ms-user-select: none;
    -moz-user-select: none;
    user-select: none;
}

.base{
    margin: 30px;
    background-color: rgb(200, 204, 214);
    // display: inline-block;
    float: left;
    h1{
        @extend .userNoSelect;
        text-align: left;
        margin: 0;
    }
}

.defocusbase{
    @extend .base;
    border: 8px black solid;
    transition: all 1s;
}

.focusBase{
    @extend .base;
    border: 8px rgb(255, 243, 72) solid;
    border-radius: 20%;
    transition: all 0.3s;
}

.defocusDocument{
    @extend .base;
    margin: 0;
    color: white;
    background-color: rgb(2, 2, 2);
    border: unset;
    border-radius: 0;
    transition: all 1s;
}

.focusDocumentBase{
    @extend .base;
    margin: 0;
    background-color: rgb(71, 181, 245);
    transition: all 0.3s;
}

.focusSelecting{
    background-color: red;
}

.headImgStyle{
    @extend .userNoSelect;
    z-index: 9;
}

.document_newsBox{
    // max-height: 300px;
    // max-width: 200px;
    @extend .userNoSelect;
    max-height: 200px;
    max-width: 250px;
    overflow: hidden;
    overflow-wrap: break-word;
    white-space: pre-wrap;
}

.document_open{
    @extend .userNoSelect;
    overflow-wrap: break-word;
    word-wrap: break-word;
    word-break: normal;
    white-space: pre-wrap;
}

/// transition

// @keyframes rootEnter{
//     from { transform: scale(0); }
//     80% { transform: scale(1.2); }
//     to { transform: scale(1); }
// }

// @keyframes rootLeave {
//     from { transform: scale(1); }
//     20% { transform: scale(1.2); }
//     to { transform: scale(0); }
// }

.rootTransition-enter, .rootTransition-leave-to{
    // transform: scale(0);
    opacity: 0;
}

.rootTransition-enter-active{
    // animation: 0.5s rootEnter;
    transition: opacity, 0.5s;
}

.rootTransition-leave-active{
    // animation: 0.5s rootLeave;
    transition: opacity, 0.3s;
}

.contentChange-enter, .contentChange-leave-to{
    opacity: 0;
}

.contentChange-enter-active{
    transition: opacity 0.8s ease-in;
}

.contentChange-leave-active{
    transition: opacity 0.3s ease-out;
}

</style>