<template>

    <div>
        <v-menu-item 
            v-for = "menuItem in itemList"
            class = "base"
            :key = "menuItem.id"
            ref="menuItems"
            @onclicked = "onAnItemBeenClicked"
            @clearAllSelectionState = "clearAllSelectionState"
            @onAutoCloseDropDownMenuFinished = "onAutoCloseDropDownMenuFinished"
            @onMenuItemMounted = "onMenuItemMounted"
            @onMenuItemDesdroyed = "onMenuItemDesdroyed"
        >
        </v-menu-item>
        <!-- <div id="ClearFloat"></div> -->
    </div>

</template>


<script>

import VMenuItem from './MenuItem.vue';

// class of deropDownItem;

// menu item class
class MenuItem{
    constructor(id, text, callback, dropDownItemList=null, isButton=false){
        if(this.ifParamsTypeOK(id, text, callback, isButton)){
            this.id = id;
            this.text = text;
            this.callback = callback;
            this.dropDownItemList = dropDownItemList;
            this.isButton = isButton;
        }else{
            window.console.error("Instantiate MenuItem error, use default param.");
            return MenuItem.DEFAULT;
        }
    }

    IfFieldsOK(){
        if(this.ifParamsTypeOK(this.id, this.text, this.callback, this.isButton)){
            if(this.id == "DefaultId"){
                window.console.error("MenuItem id can't be string 'DefaultId'");
                return false;
            }else {
                if(this.dropDownItemList){
                    if(Array.isArray(this.dropDownItemList)){
                        for(let dropDownItem of this.dropDownItemList){
                            if(!dropDownItem.IfFieldsOK()){
                                return false;
                            }
                        }
                    }else{
                        window.console.error("MenuBar's dropDownItemList must be an array.");
                    }
                }
                return true;
            }
        }else{
            return false;
        }
    }

    ifParamsTypeOK(id, text, callback, isButton){
        if(typeof(id) != "string" && typeof id != "number"){
            window.console.error("MenuItem param id must be string or number.");
            return false;
        }

        if(typeof(text) != "string"){
            window.console.error("MenuItem param text must be string.");
            return false;
        }

        if(callback !== null || callback !== undefined){
            if(typeof(callback) != "function"){
                window.console.error("MenuItem param callback must be function.");
                return false;
            }
        }

        if(typeof(isButton) !="boolean"){
            window.console.error("MenuItem param isButton must be boolean.");
            return false;
        }

        return true;
    }
}

MenuItem.DEFAULT = new MenuItem("DefaultId", "Default Item", ()=>{});
Object.defineProperty(MenuItem.DEFAULT, "id", { writable: false});
Object.defineProperty(MenuItem.DEFAULT, "text", { writable: false});
Object.defineProperty(MenuItem.DEFAULT, "callback", { writable: false});

// private field
let menuBarsCurrentDropDownItemMap = new Map();
let mountedMenuItemDOM_Map = new Map();

export default {
    
    name:"MenuBar",

    data(){
        return{

            o_currentDropDownItem: null,

            itemList:[
                // new MenuItem("0", "default", ()=>{window.alert("This is default menuItem!")})
            ]
        }
    },

    methods:{

        /// setter & getter

        // Change item properties
        SetPropertiesByItemIndex(index = 0, menuItem = MenuItem.DEFAULT){
            if(menuItem.IfFieldsOK()){
                this.setPropertyByItemIndex(index, menuItem);
            }
        },

        // Set menu items float, need input "left" or "right"
        SetMenuItemsFloat(dir = "left"){
            if(dir == "left"){
                this.$refs.menuItems.style["float"] = "left";
            }else if(dir == "right"){
                this.$refs.menuItems.style["float"] = "right";
            }else{
                window.console.error("MenuBar SetMenuItemsFloat error, param must be string 'left' or 'right'.");
            }
        },

        
        setPropertyByItemIndex(index, menuItem){
            let itemArray = this.$refs.menuItems;
            if(itemArray){
                let item = itemArray[index];
                if(item){
                    item.id = menuItem.id;
                    item.SetText(menuItem.text);
                    item.SetClickedCallBack(menuItem.callback);
                    item.SetDropDownItemsByList(menuItem.dropDownItemList);
                    item.SetIsButton(menuItem.isButton);
                }else{
                    window.console.error("MenuBar SetCallBackByItemID must be a Number and can't over flower!");
                }
            }
        },

        /// events

        // 不是业务功能回调，只是下拉菜单的基础表现回调，业务回调用SetClickedCallBack
        onAnItemBeenClicked(event, menuItemId){
            let itemArray = this.$refs.menuItems;
            if(itemArray){
                if(!this.o_currentDropDownItem){
                    for(let menuItem of itemArray){
                        if(menuItem.id === menuItemId){
                            this.o_currentDropDownItem = menuItem;
                            this.o_currentDropDownItem.Focus();
                            break;
                        }
                    }
                }else{
                    if(this.o_currentDropDownItem.id === menuItemId){
                        if(this.o_currentDropDownItem.isDropDownItem){
                            this.o_currentDropDownItem.Defocus();
                            this.o_currentDropDownItem = null;
                        }else{
                            this.o_currentDropDownItem.Focus();
                        }
                    }else{
                        for(let menuItem of itemArray){
                            if(menuItem.id === menuItemId){
                                if(this.o_currentDropDownItem.isDropDownItem){
                                    if(menuItem.isDropDownItem){
                                        this.o_currentDropDownItem.Defocus(true);
                                        this.o_currentDropDownItem = menuItem;
                                    }else{
                                        this.o_currentDropDownItem.Defocus();
                                        this.o_currentDropDownItem = menuItem;
                                        this.o_currentDropDownItem.Focus();
                                    }
                                }else{
                                    this.o_currentDropDownItem.Defocus();
                                    this.o_currentDropDownItem = menuItem;
                                    this.o_currentDropDownItem.Focus();
                                }
                            }
                        }
                    }
                }
            }
            menuBarsCurrentDropDownItemMap.set(this, this.o_currentDropDownItem);
        },

        clearAllSelectionState(){
            let itemArray = this.$refs.menuItems;
            for(let menuItem of itemArray){
                menuItem.Defocus();
            }
            this.o_currentDropDownItem = null;
        },

        onAutoCloseDropDownMenuFinished(){
            if(this.o_currentDropDownItem){
                //may be cancelled before animation finished
                this.o_currentDropDownItem.Focus();
            }
        },

        onMenuItemMounted(menuItemDOM){
            if(!mountedMenuItemDOM_Map.has(menuItemDOM)){
                mountedMenuItemDOM_Map.set(menuItemDOM, true);
                // window.console.log(menuItemDOM);
            }
        },

        onMenuItemDesdroyed(menuItemDOM){
            if(mountedMenuItemDOM_Map.has(menuItemDOM)){
                delete mountedMenuItemDOM_Map[menuItemDOM];
                // window.console.log(menuItemDOM);
            }
        },

        /// behaviours
        
        AddAnItem(menuItem, index = undefined){
            if(menuItem){
                if(menuItem.IfFieldsOK && typeof(menuItem.IfFieldsOK) == "function"){
                    if(menuItem.IfFieldsOK()){
                        if(index == undefined){
                            this.itemList.push(menuItem);
                            let itemListIndex = this.itemList.length-1;
                            this.$nextTick(()=>{
                                this.setPropertyByItemIndex(itemListIndex, menuItem);
                            });
                        }else{
                            this.itemList.splice(index, 0, menuItem);
                            this.$nextTick(()=>{
                                this.setPropertyByItemIndex(index, menuItem);
                            });
                        }
                    }
                }
                
            }
        },

        RemoveAnItem(index = undefined){
            if(this.itemList.length > 0){
                if(index == undefined){
                    this.itemList.pop();
                }else{
                    this.itemList.splice(index, 1);
                }
            }
        },
    },

    components:{
        VMenuItem,
    },

    /* Other */

    // classes
    MenuItem,
    DropDownItem: VMenuItem.DropDownItem,

    OnWindowClick(e){
        // window.console.log(e.target);
        if(!mountedMenuItemDOM_Map.has(e.target)){
            let keyEnum = menuBarsCurrentDropDownItemMap.keys();
            let menuBar = keyEnum.next().value;
            while(menuBar){
                let dropDownItem = menuBarsCurrentDropDownItemMap.get(menuBar);
                if(dropDownItem){
                    // window.console.log(lastClickedMenuItem.menuItemDOM);
                    dropDownItem.Defocus();
                    menuBar.o_currentDropDownItem = null;
                    menuBarsCurrentDropDownItemMap.set(menuBar, null);
                }
                menuBar = keyEnum.next().value;
            }
        }
    }
}

</script>

<style lang="scss" scoped>

.base{
    float:right;
}

</style>