import {BaseData} from "../../base/BaseData.js";

export let CSSTips = new BaseData(
    "CSS相关知识",

    "CSS相关的一些值得注意的知识。",
    
    `1.CSS属性前缀

    （1）-moz代表firefox浏览器私有属性
    
    （2）-ms代表ie浏览器私有属性
    
    （3）-webkit代表safari、chrome私有属性
    
    注意这些是为了兼容老版本的写法，比较新版本的浏览器都支持直接写，如：border-radius。
    
    现在比较流行的一个概念就是：不太关键的样式，可以不考虑兼容，比如你说的圆角，并不影响内容的显示，如果客户浏览器太旧，就让他看到旧的方形效果就是，一定要看到新效果，只能建议客户升级浏览器。
    
    
    2.浏览器引擎
    
    WebKit 是一个开源的浏览器引擎，与之相对应的引擎有Gecko（Mozilla Firefox 等使用）和Trident（也称MSHTML，IE 使用）。
    
    3.居中的三种写法
    
    （1）绝对定位的水平垂直居中
    
    提前知道元素大小：
    
    <style>
        .center-vertical{
            width: 100px;
            height: 100px;
            background: orange;
            position: absolute;
            top: 50%;
            left: 50%;
            margin-top: -50px; /*高度的一半*/
            margin-left: -50px; /宽度的一半*/
        }
    </style>
    
    CSS3不知道元素的大小：
    
    <style>
    .content{
        background: orange;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate( -50%, -50%);
    }
    </style>
    
    transform 中 translate 偏移的百分比是相对于自身大小而说的。
    
    （2）margin:auto;
    
    <style>
    .center-vertical{
        width: 100px;
        height: 100px;
        background: orange;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
    }
    </style>
    
    （3）弹性布局
    
    <style>
    html,body{
        width: 100%;
        height: 100%;
        margin: 0;
        padding: 0;
    }
    body{
        display: flex;
        align-items: center;/*定义body的元素垂直居中*/
        justify-content: center;/*定义body的元素水平居中*/
    }
    .content{
        width: 300px;
        height: 300px;
        background: orange;
    }
    </style>
    
    （4）vertical-align:middle; 垂直方向居中
    
    <style>
    .big-box{
        width: 500px;
        height: 400px;
        background: green;
        text-align: center;
    }
    .box{
        line-height: 400px;
    }
    img{
        vertical-align: middle;
    }
    </style>
    
    ...
    
    <div class="big-box">
        <span class="box">
            <img src="ermao.jpg"/>
        </span>
    </div>
    
    （5）display:table;
    
    <style>
    .parent{
        width: 300px;
        height: 300px;
        background: orange;
        text-align: center;
        display: table;
    }
    .son{
        display: table-cell;
        background-color: yellow;
        vertical-align: middle;
    }
    </style>
    
    （6）相对定位
    
    <style>
    html,body{
        width: 100%;
        height: 100%;
        margin: 0;
        padding: 0;
    }
    .content{
        width: 300px;
        height: 300px;
        background: orange;
        margin: 0 auto;/*水平居中*/
        position: relative;/*设置position*/
        top: 50%; /*偏移*/
        /*margin-top: -150px;*/    /*第一种：margin-top*/
        transform: translateY(-50%);/*第二种：transform：转换*/
    }
    </style>`,
);