<template>

    <div :class = "o_baseStyle"
        ref="root"
        >
        <button :class="o_menuButtonStyle"
            @touchstart="onTouchStart"
            @touchend="onTouchEnd"
        >
            {{s_buttonWord}}
        </button>
        
        <transition name="menuItemsTrans"
            @before-enter="BeforeMenuItemEntered"
            @after-enter="OnMenuItemEntered"
            @after-leave="OnMenuItemsLeft"
        >
            <div v-show="b_ifShowMenuItems"
                class="mainContainContainer"
            >
                <div class="sideItemsViewRect"
                    ref="sideItemsViewRect"
                >
                    <div 
                        class="sideMenuItemsContainer"
                        @touchstart="onSideMenuItemsContainerTouchStart"
                        @touchmove="onSideMenuItemsContainerTouchMove"
                        ref="sideMenuItemsContainer"
                        >
                        <v-side-menu-item
                            v-for = "sideMenuItem in o_itemList"
                            :key = "sideMenuItem.s_id"
                            ref="sideMenuItems"
                            @onclicked = "onAnItemBeenClicked"
                            @onMenuItemMounted = "onMenuItemMounted"
                            @onMenuItemDesdroyed = "onMenuItemDesdroyed"
                        >
                        </v-side-menu-item>
                    </div>
                </div>

                <button 
                    :class="o_gobackButtonStyle" 
                    @touchstart.prevent="onReturnTouchStart"
                    @touchend.prevent="onReturnTouchEnd"
                >
                    {{s_goBack}}
                </button>
            </div>
        </transition>

        <!-- <div id="ClearFloat"></div> -->
    </div>

</template>


<script>

import VSideMenuItem from './SideMenuItem.vue';

// class of deropDownItem;

// menu item class
class SideMenuItem{
    constructor(s_id, s_text, f_callback, o_children=null){
        if(this.ifParamsTypeOK(s_id, s_text, f_callback, o_children)){
            
            this.s_id = s_id;
            
            this.s_text = s_text;
            
            this.f_callback = f_callback;
            
            this.o_children = o_children;

            if(this.o_children){
                for(let child of this.o_children){
                    child.o_parent = this;
                }
            }
        }else{

            window.console.error("Instantiate SideMenuItem error, use default param.");
            
            return SideMenuItem.DEFAULT;
        }
    }

    IfFieldsOK(){

        if(this.ifParamsTypeOK(this.s_id, this.s_text, this.f_callback, this.o_children)){

            if(this.s_id != "DefaultId"){

                if(this.o_children){

                    for(let child of this.o_children){
                        
                        if(!child.IfFieldsOK()){
                            
                            return false;
                        }
                    }
                }
                
                return true;

            }else {

                window.console.error("SideMenuItem id can't be string 'DefaultId'");
                
                return false;
            }
        }else{

            return false;
        }
    }

    ifParamsTypeOK(s_id, s_text, f_callback, o_children){

        if(typeof(s_id) != "string"){
            window.console.error("SideMenuItem param id must be string or number.");
            return false;
        }

        if(typeof(s_text) != "string"){
            window.console.error("SideMenuItem param text must be string.");
            return false;
        }

        if(f_callback !== null || f_callback !== undefined){
            if(typeof(f_callback) != "function"){
                window.console.error("SideMenuItem param callback must be function.");
                return false;
            }
        }

        if(o_children && !Array.isArray(o_children)){
            window.console.error("SideMenuItem "+ this.s_id + "'s o_children must be an array.");
            return false;
        }

        return true;
    }
}

SideMenuItem.DEFAULT = new SideMenuItem("DefaultId", "Default Item", ()=>{});

// 设置只读属性
Object.defineProperty(SideMenuItem.DEFAULT, "s_id", { writable: false});
Object.defineProperty(SideMenuItem.DEFAULT, "s_text", { writable: false});
Object.defineProperty(SideMenuItem.DEFAULT, "f_callback", { writable: false});

// private field
let mountedMenuItemDOM_Map = new Map();
let o_sideMenuBarSet = new Set();

export default {
    
    name:"MenuBar",

    data(){
        return{
            
            /// public

            f_onReturnClicked: null,

            /// private

            // 逻辑赋值，记得样式更改了之后这里也要同步修改，并不会反向赋值给样式
            
            n_menuButtonHalfWidth: 50,
            
            n_menuButtonHalfHeight: 50,
            
            n_maxPosX: 0,

            n_maxPosY: 0,

            n_pressingClock: -1,

            n_menuItemsMoveLimitLen: 0,

            n_menuItemsMoveSpeedRate: 0.5,

            n_menuItemsMoveStartPosY: 0,

            n_menuItemsLastTimeTop: 0,

            b_ispressing: false,
            
            b_ifShowMenuItems: false,

            b_ifShowAnotherChildren: false,

            s_buttonWord: "菜单",

            s_goBack:"向上",

            o_baseStyle:{
                base_left: true,
                base_right: false,
            },
            
            o_menuButtonStyle:{
                main_button_normal: true,
                main_button_pressing: false,
            },

            o_gobackButtonStyle:{
                gobackButton_normal: true,
                gobackButton_pressing: false,
            },

            o_rootList: null,

            o_currentParent: null,

            o_itemList:[
                // new SideMenuItem("0", "default", ()=>{window.alert("This is default sideMenuItem!")})
            ],
        }
    },

    methods:{

        /// public

        // setter & getter

        // Change item properties
        SetPropertiesByItemIndex(index = 0, sideMenuItem = SideMenuItem.DEFAULT){

            if(sideMenuItem.IfFieldsOK()){
            
                this.setPropertyByItemIndex(index, sideMenuItem);
            }
        },

        // Set side menu bar style, need input "left" or "right"
        SetSideMenuBarStyle(dir = "left"){

            if(dir == "left"){
                
                this.o_baseStyle.base_left = true;
                
                this.o_baseStyle.base_right = false;
            
            }else if(dir == "right"){

                this.o_baseStyle.base_right = true;
                
                this.o_baseStyle.base_left = false;
            
            }else{
            
                window.console.error("MenuBar SetMenuItemsFloat error, param must be string 'left' or 'right'.");
            }
        },

        SetCurrentListAsRoot(){
            this.o_rootList = this.o_itemList;
        },

        // events

        OnWindowTouchMove(o_touchEvent){

            if(this.b_ispressing){

                let touchX = o_touchEvent.changedTouches[0].clientX;
                let touchY = o_touchEvent.changedTouches[0].clientY;

                // window.console.log("document.scorllx " + document.documentElement.scrollLeft + " y is " + document.documentElement.scrollTop);
                // let scrollLeft = document.body.scrollLeft? document.body.scrollLeft : document.documentElement.scrollLeft;
                // let scrollTop = document.body.scrollTop? document.body.scrollTop : document.documentElement.scrollTop;
                let root = this.$refs.root;
                let left = touchX - this.n_menuButtonHalfWidth;
                let top = touchY - this.n_menuButtonHalfHeight;

                left = left < 0? 0 : left;
                left = left > this.n_maxPosX ? this.n_maxPosX : left;
                top = top < 0? 0 : top;
                top = top > this.n_maxPosY ? this.n_maxPosY : top;

                root.style.left = left + "px";
                root.style.top = top + "px";

                // window.console.log("left is "+root.style.left);
                // window.console.log("top is "+root.style.top);
                // window.console.log("document width is " + document.documentElement.scrollWidth);
                // window.console.log("document height is " + document.documentElement.scrollHeight);
            }
        },

        //other

        AddAnItem(sideMenuItem, index = undefined){
            if(sideMenuItem){
                if(sideMenuItem.IfFieldsOK && typeof(sideMenuItem.IfFieldsOK) == "function"){
                    if(sideMenuItem.IfFieldsOK()){
                        if(index == undefined){
                            this.o_itemList.push(sideMenuItem);
                            let itemListIndex = this.o_itemList.length-1;
                            this.$nextTick(()=>{
                                this.setPropertyByItemIndex(itemListIndex, sideMenuItem);
                            });
                        }else{
                            this.o_itemList.splice(index, 0, sideMenuItem);
                            this.$nextTick(()=>{
                                this.setPropertyByItemIndex(index, sideMenuItem);
                            });
                        }
                    }
                }
                
            }
        },

        RemoveAnItem(index = undefined){
            if(this.o_itemList.length > 0){
                if(index == undefined){
                    this.o_itemList.pop();
                }else{
                    this.o_itemList.splice(index, 1);
                }
            }
        },

        RefreshMenuItemsData(){
            let o_menuItemDoms = this.$refs.sideMenuItems;
            for(let index in o_menuItemDoms){
                this.setPropertyByItemIndex(index, this.o_itemList[index]);
            }
        },

        /// private

        setPropertyByItemIndex(index, sideMenuItem){
            
            let itemArray = this.$refs.sideMenuItems;

            if(itemArray){
            
                let item = itemArray[index];
            
                if(item){
  
                    item.s_id = sideMenuItem.s_id;
                    
                    item.SetText(sideMenuItem.s_text);
                    
                    item.SetClickedCallBack(sideMenuItem.f_callback);
                    
                    item.SetChildren(sideMenuItem.o_children);
                
                }else{
                    window.console.error("MenuBar SetCallBackByItemID must be a Number and can't over flower!");
                }
            }
        },

        /// events

        onTouchStart(){

           this.b_ispressing = true;
           
           this.o_menuButtonStyle.main_button_normal = false;
           
           this.o_menuButtonStyle.main_button_pressing = true;
        },

        onTouchEnd(){

            this.b_ispressing = false;
            
            this.o_menuButtonStyle.main_button_normal = true;

            this.o_menuButtonStyle.main_button_pressing = false;

            if(this.b_ifShowMenuItems){

                if(this.o_currentParent){
                    
                    this.b_ifShowAnotherChildren = true;
                        
                    this.o_currentParent = this.o_currentParent.o_parent?
                        
                        this.o_currentParent.o_parent :
                        
                        null;

                }
                    
                this.b_ifShowMenuItems = false;

            }else{

                this.b_ifShowMenuItems = true;
            }
        },

        onReturnTouchStart(){
            
            this.o_gobackButtonStyle.gobackButton_normal = false;

            this.o_gobackButtonStyle.gobackButton_pressing = true;
        },

        onReturnTouchEnd(){
            this.o_gobackButtonStyle.gobackButton_normal = true;
            this.o_gobackButtonStyle.gobackButton_pressing = false;
            if(this.f_onReturnClicked){
                this.f_onReturnClicked();
            }
        },

        onSideMenuItemsContainerTouchStart(o_touchEvent){
            
            this.n_menuItemsLastTimeTop = this.$refs.sideMenuItemsContainer.offsetTop - this.$refs.sideItemsViewRect.offsetTop;

            this.n_menuItemsMoveStartPosY = o_touchEvent.changedTouches[0].clientY;
        },

        onSideMenuItemsContainerTouchMove(o_touchEvent){

            let deltaY = o_touchEvent.changedTouches[0].clientY - this.n_menuItemsMoveStartPosY;

            deltaY *= this.n_menuItemsMoveSpeedRate;

            deltaY += this.n_menuItemsLastTimeTop;

            deltaY = deltaY > 0 ? 0 : deltaY;

            deltaY = deltaY < this.n_menuItemsMoveLimitLen ? this.n_menuItemsMoveLimitLen : deltaY;

            this.$refs.sideMenuItemsContainer.style.top = deltaY + "px";
        },

        onAnItemBeenClicked(o_vSideMenuItem){

            //TODO: 切换动画
            if(o_vSideMenuItem.o_children){

                this.b_ifShowAnotherChildren = true;

                this.o_currentParent = o_vSideMenuItem;

            }

            this.b_ifShowMenuItems = false;
        },

        onMenuItemMounted(menuItemDOM){
            if(!mountedMenuItemDOM_Map.has(menuItemDOM)){
                mountedMenuItemDOM_Map.set(menuItemDOM, true);
                // window.console.log(menuItemDOM);
            }
        },

        onMenuItemDesdroyed(menuItemDOM){
            if(mountedMenuItemDOM_Map.has(menuItemDOM)){
                delete mountedMenuItemDOM_Map[menuItemDOM];
                // window.console.log(menuItemDOM);
            }
        },

        BeforeMenuItemEntered(){
        
            this.$refs.sideMenuItemsContainer.style.top = 0 + "px";
        },

        OnMenuItemEntered(){
            
            this.s_buttonWord = "返回";

            let itemsHeight = this.$refs.sideMenuItemsContainer.offsetHeight;

            let menuItemsStartTopGap = this.$refs.sideMenuItemsContainer.offsetTop - this.$refs.sideItemsViewRect.offsetTop;

            this.n_menuItemsMoveLimitLen = this.$refs.sideItemsViewRect.offsetHeight - itemsHeight - menuItemsStartTopGap;

            this.n_menuItemsMoveLimitLen = this.n_menuItemsMoveLimitLen > 0 ? 0 : this.n_menuItemsMoveLimitLen;
        },

        OnMenuItemsLeft(){

            if(this.b_ifShowAnotherChildren){

                this.o_itemList = this.o_currentParent ? this.o_currentParent.o_children : this.o_rootList;

                this.$nextTick(()=>{
                    this.RefreshMenuItemsData();
                });

                this.b_ifShowMenuItems = true;
            
                this.b_ifShowAnotherChildren = false;

            }else{

                this.s_buttonWord = "菜单";
            
            }
        }

    },

    mounted(){

        this.n_maxPosX = document.body.scrollWidth? document.body.scrollWidth : document.documentElement.scrollWidth;

        this.n_maxPosX -= (this.n_menuButtonHalfWidth * 2);

        this.n_maxPosY = document.body.scrollHeight? document.body.scrollHeight : document.documentElement.scrollHeight;

        this.n_maxPosY *= 0.5;

        if(!o_sideMenuBarSet.has(this)){
            o_sideMenuBarSet.add(this);
        }
    },

    destroyed(){
        if(o_sideMenuBarSet.has(this)){
            o_sideMenuBarSet.delete(this);
        }
    },

    components:{
        VSideMenuItem,
    },

    /* Other */

    OnWindowTouchMove(o_touchEvent){
        for(let sideMenuBar of o_sideMenuBarSet){
            sideMenuBar.OnWindowTouchMove(o_touchEvent);
        }        
    },

    // classes
    SideMenuItem,
}

</script>

<style lang="scss" scoped>

.base_left{
    top: 10%;
    left: 10%;
    height: 50%;
    position:fixed;
    z-index: 100;
}

.base_right{
    top: 10%;
    right: 10%;
    height: 50%;
    position:fixed;
    z-index: 100;
}

.main_button_normal{
    width: 100px;
    height: 10%;
    border: 5px black solid;
    background-color: white;
    color: black;
    font-size: 30px;
    font-style: oblique;
    transition: all 0.5s ease;
    // left: 50px;
    // top: 20px;
    // position: relative;
}

.main_button_pressing{
    width: 100px;
    height: 10%;
    border: 10px rgb(78, 245, 0) solid;
    background-color: yellow;
    color: rgb(0, 12, 122);
    font-size: 30px;
    font-style: oblique;
    transition: all 0.5s ease;
}

.mainContainContainer{
    height: 90%;
    transform-origin: 50% 0;
}

.sideItemsViewRect{
    margin-top: 10px;
    height: 80%;
    overflow: hidden;
    background-color: gray;
    border: 5px black solid;
}

.sideMenuItemsContainer{
    position: relative;
}

.user_noselect{
    -moz-user-select:none;/*火狐*/
    -webkit-user-select:none;/*webkit浏览器*/
    -ms-user-select:none;/*IE10*/
    -khtml-user-select:none;/*早期浏览器*/
    pointer-events: none;
    user-select:none;
}

.gobackButton_normal{
    margin: 10px 0;
    height: 10%;
    border: 10px black solid;
    color: white;
    text-align: center;
    font-size: 30px;
    padding: 10px;
    background-color: rgba($color: #0a5cf5, $alpha: 1.0);
    transition: all 0.5s ease;
}

.gobackButton_pressing{
    margin: 10px 0;
    height: 10%;
    border: 15px black solid;
    color: rgb(0, 0, 0);
    text-align: center;
    font-size: 30px;
    padding: 10px;
    background-color: rgba($color: #e1ff39, $alpha: 1.0);
    transition: all 0.5s ease;
}

// anims

@keyframes scaleIn {
    from { transform: scaleY(0); }
    20% { transform: scaleY(1.2); }
    to   { transform: scaleY(1); }
}

@keyframes scaleOut {
    from { transform: scaleY(1); }
    80% { transform: scaleY(1.2); }
    to   { transform: scaleY(0); }
}

.menuItemsTrans-active-base{
    pointer-events: none;
}

.menuItemsTrans-enter-active{
    animation: 0.5s scaleIn;
    @extend .menuItemsTrans-active-base;
}

.menuItemsTrans-leave-active{
    animation: 0.5s scaleOut;
    @extend .menuItemsTrans-active-base;
}

</style>