<template>
    <div id="SideMenuItem" 
        
        :class="o_itemStyle"
        
        @touchstart.prevent="OnTouchStart"
        
        @touchend.prevent="OnTouchEnd"
        
        ref="menuItem">
        
        <p>{{s_text}}</p>

    </div>
</template>

<script>

let n_pressingOverTime = 300; //长按多久算失效

export default {
    name:"MenuItem",

    props:[
        "onclick"
    ],

    data(){
        return {

            /// public
            
            o_parent: null,

            o_children: [],

            /// private

            n_pressingClockId: undefined,

            b_ifPressingOverTime: false,

            b_ifSelected: false,

            s_text:"default",
            
            f_callbackFunc: null,

            o_itemStyle:{
                
                item_normal: true,

                item_pressing: false,
            },

            o_menuItemDOM: null,
        }
    },

    methods:{

        // setter & getter
        SetText(s_text){
            this.s_text = s_text;
        },

        SetClickedCallBack(f_callbackFunc){
            this.f_callbackFunc = f_callbackFunc;
        },

        SetChildren(o_children){
            this.o_children= o_children;
        },

        // events

        OnTouchStart(){
            this.o_itemStyle.item_normal = false;
            
            this.o_itemStyle.item_pressing = true;
            
            if(!this.n_pressingClockId){

                this.n_pressingClockId = setTimeout(()=>{
                    
                    this.b_ifPressingOverTime = true;

                }, n_pressingOverTime);
            
            }
        },

        OnTouchEnd(){
            
            this.o_itemStyle.item_normal = true;
            
            this.o_itemStyle.item_pressing = false;

            if(!this.b_ifPressingOverTime){

                if(this.f_callbackFunc){
                
                    this.f_callbackFunc();

                    this.$emit("onclicked", this);
                }
            }

            clearTimeout(this.n_pressingClockId);

            this.n_pressingClockId = undefined;

            this.b_ifPressingOverTime = false;
            
        }
    },

    mounted(){
        this.ifShowDropDownMenu = false;
        this.o_menuItemDOM = this.$refs.menuItem;
        this.$emit("onMenuItemMounted", this.o_menuItemDOM);
    },

    destroyed(){
        this.$emit("onMenuItemDesdroyed", this.o_menuItemDOM);
    },
}

</script>

<style lang="scss" scoped>

.base{
    margin: 10px 0;
    border: 10px black solid;
    text-align: center;
    font-size: 30px;
    padding: 10px;
    background-color: rgba($color: #ffffff, $alpha: 1.0);
}

.item_normal{
    @extend .base;
    transition: all 0.5s ease;
}

.item_pressing{
    @extend .base;
    color: white;
    border: 15px black solid;
    background-color: rgba($color: #0a5cf5, $alpha: 1.0);
    transition: all 0.5s ease;
}

</style>





