<template>
  <div ref="root">
    <v-title-zone 
      @onHome="onHome"
      @onCode="onCode"
      @onArticle="onArticle"
      @onReturn="onReturn"
      ref="titleZone"
    >
    </v-title-zone>

    <v-content-zone ref="contentZone"></v-content-zone>
    
    <v-footer-zone :authorInfo="footerText" ref="footerZone"></v-footer-zone>
  </div>
</template>

<script>

import commonTools from './js/commonTools.js';
import VTitleZone from './components/body/TitleZone.vue';
import VFooterZone from './components/body/FooterZone.vue';
import VContentZone from './components/body/ContentZone.vue';

export default {
  name: 'app',
  
  components: {
    VTitleZone,
    VContentZone,
    VFooterZone,
  },
  data(){
      return {
          footerText: "Author is GSan QQ: 2087653648",
      }
  },

  beforeCreate(){
    // this.$router.push("/home");
  },

  mounted(){

    let windowLeftHeight = window.screen.availHeight;//window.screen.height;

    if(!commonTools.isRunningOnPC){
      windowLeftHeight *= window.devicePixelRatio;
    }

    // window.console.log("windowLeftHeight is " + windowLeftHeight);

    //窗口剩余高度
    windowLeftHeight -= parseFloat(this.$refs.titleZone.$refs.root.offsetHeight);

    // window.console.log("offsetHeight is " + this.$refs.titleZone.$refs.root.offsetHeight);
    // window.console.log("titleZoneMinHeight is " + this.$refs.titleZone.titleZoneMinHeight);

    //做整体界面适配行为
    windowLeftHeight -= parseFloat(this.$refs.footerZone.$refs.root.offsetHeight);
    // window.console.log("windowLeftHeight2 is "+ windowLeftHeight);

    //最后设置内容板块的高度
    this.$refs.contentZone.$refs.root.style["min-height"] = windowLeftHeight + "px";

    // window.console.log("devicePixelRatio is "+window.devicePixelRatio);
  },

  methods:{

    /// events
    onHome(){
    },

    onCode(){
    },

    onArticle(index){
      window.console.log("onArticle index is " + index);
    },

    onReturn(){
      this.$refs.contentZone.Return();
    }
  }
}

/// 全局事件的注册和分发

// 窗口事件队列
let windowClickedEventQueue = [];
// 注册窗口点击事件回调
windowClickedEventQueue.push(VTitleZone.OnWindowClick);
window.onclick = function(o_event){

  // window.console.log(e.target);

  for(let windowEvent of windowClickedEventQueue){
    // if(windowEvent && typeof(windowEvent) == "function"){
    windowEvent(o_event);
    // }
  }
}

// 窗口大小变化事件队列
let windowResizeEventQueue = [];
windowResizeEventQueue.push(VContentZone.OnWindowResize);
window.onresize = function(){
  for(let windowResizeEvent of windowResizeEventQueue){
    windowResizeEvent();
  }
}

// 手机滑动屏幕事件队列
let windowTouchMoveEventQueue = [];
windowTouchMoveEventQueue.push(VTitleZone.OnWindowTouchMove)
window.ontouchmove = function(o_touchEvent){
  // window.console.log(e);
  for(let windowTouchMoveEvent of windowTouchMoveEventQueue){
    windowTouchMoveEvent(o_touchEvent);
  }
}

</script>

<style lang="scss">

[v-cloak] {
    display: none;
}

html, body {
    margin: 0;
    padding: 0;
}

</style>
