import {BaseData} from "../../base/BaseData.js";

export let ElementZOrder = new BaseData(
    "HTML中的层级",
    "元素间的层级关系",
    `总结：标准流盒子，低于浮动的盒子，浮动的盒子又低于定位的盒子。定位高于浮动，浮动高于标准流。（高低和占不占位置无关）

    用法：
    
    必须有定位。（除去static之外）。
    
    给定z-index的值为层级的值。（不给默认为0）
    
    （层级为0的盒子，也比标准流和浮动高。）
    （层级为负数的盒子，比标准流和浮动低。）
    （层级不取小数）
    （层级一样，后面的盒子比前面的层级高。）
    （浮动或者标准流的盒子，后面的盒子比前面的层级高。）
    
    定位中：abselute是不占位置的，relative是站位的的。而层级的高低，是和占不占位置没有关系的。
    
    
    ------------------------- position/z-order=0
    ------------------------- float:left
    ------------------------- 标准流盒子
    ------------------------- position/z-order=-1`,
);